import { MouseEventData } from '../../IO/EventInteraction/MouseInteraction';

import { Asset } from './AssetWrapper';

import { ActiveCommand } from './Core/ActiveCommand';
import { ControllerTheme } from './Core/ControllerTheme';
import { PullDownMenuWrapper } from './WrapperPullDownMenu/PullDownMenuWrapper';
import { IndependentPanelWrapper } from './WrapperIndependentPanel/IndependentPanelWrapper';
import { UICanvas } from './Core/UICanvas';

export class NUI {
    asset: Asset;
    theme: ControllerTheme;
    themeType: string = 'default';
    activeCommand: ActiveCommand;

    independentPanelWrapper: IndependentPanelWrapper;
    pullDownMenuWrapper: PullDownMenuWrapper;

    divHost: HTMLElement;
    width: number;
    height: number;

    indepCanvas: UICanvas;
    pullDownCanvas: UICanvas;

    private _stopPropagation: boolean = false;

    constructor(divHost: HTMLElement, needUI: boolean = true) {
        this.divHost = divHost;
        this.width = this.divHost.clientWidth;
        this.height = this.divHost.clientHeight;
        // console.log(needUI);
        this.asset = new Asset();
        this.theme = new ControllerTheme();

        this.activeCommand = new ActiveCommand(this);

        this.independentPanelWrapper = new IndependentPanelWrapper(this, 0, 0, this.width, this.height);
        this.indepCanvas = new UICanvas(this);
        if (needUI) {
            this.indepCanvas.Render = (ctx: CanvasRenderingContext2D) => this.independentPanelWrapper.indePanelRender.Render(ctx);
        }

        this.pullDownMenuWrapper = new PullDownMenuWrapper(this, 0, 0, this.width, 25);
        this.pullDownCanvas = new UICanvas(this);
        if (this.pullDownCanvas.uiCanvas.style.zIndex) {
            const zIndex = `${((+this.pullDownCanvas.uiCanvas.style.zIndex) + 1)}`;
            if (zIndex) { this.pullDownCanvas.uiCanvas.style.zIndex = zIndex; }
        }
        if (needUI) {
            this.pullDownCanvas.Render = (ctx: CanvasRenderingContext2D) => this.pullDownMenuWrapper.menuRenderer.Render(ctx);
        }
        this.pullDownMenuWrapper.ShowMenu(true);

    }
    public ShiftTheme(v: string) {
        if (v === 'red') { this.theme.ShiftTheme(this.asset.themeRed); this.themeType = 'red'; return; }
        if (v === 'blue') { this.theme.ShiftTheme(this.asset.themeBlue); this.themeType = 'blue'; return; }
        if (v === 'purple') { this.theme.ShiftTheme(this.asset.themePurple); this.themeType = 'purple'; return; }
        if (v === 'green') { this.theme.ShiftTheme(this.asset.themeGreen); this.themeType = 'green'; return; }
        if (v === 'orange') { this.theme.ShiftTheme(this.asset.themeOrange); this.themeType = 'orange'; return; }
        if (v === 'gray') { this.theme.ShiftTheme(this.asset.themeGray); this.themeType = 'gray'; return; }
        if (v === 'darkGray') { this.theme.ShiftTheme(this.asset.themeDarkgray); this.themeType = 'darkGray'; return; }
        if (v === 'black') { this.theme.ShiftTheme(this.asset.themeBlack); this.themeType = 'black'; return; }
    }
    public MouseInteractionMenu(m: MouseEventData) {
        this.activeCommand.MouseEventController(m);
    }
    public Resize(width: number, height: number) {
        this.indepCanvas.ResizeCanvas(width, height);
        this.independentPanelWrapper.Resize(width, height);
        this.pullDownCanvas.ResizeCanvas(width, height);
        this.pullDownMenuWrapper.Resize(width, height);
    }
    public set stopPropagation(v: boolean) {
        this._stopPropagation = v;

        if (v) {
            // console.log(v);
            this.BindingStoppropagation(v);
            setTimeout(() => {
                this._stopPropagation = false;
                this.BindingStoppropagation(false);
            }, 500);
        }
    }
    public get stopPropagation() {
        return this._stopPropagation;
    }
    public BindingStoppropagation(v: boolean) {
        //
    }
    public set enablePan(v: boolean) {
        this.BindingEnablePan(v);
    }
    public BindingEnablePan(v: boolean) {
        //
    }
    // tslint:disable-next-line:no-any
    public BindingActiveCommandController(app: string, ac: any) {
        this.pullDownMenuWrapper.BuildMenuContent(app, ac);
    }
    public Destroy() {
        this.indepCanvas.isActive = false;
        this.pullDownCanvas.isActive = false;
    }
}
