import { NVector3 } from '../../../../';

export class MenuPreset {

    static RoundedRectFill(ctx: CanvasRenderingContext2D, colBG: string, vs: NVector3[], r: number, xOff: number = 0, isShadow: boolean = true) {
        // let color = s.backgroundColor;
        let radius = r; // parseFloat(s.borderRadius);
        // console.log(color);
        // ctx.save();
        ctx.lineWidth = 2; // parseFloat(s.lineWidth);
        ctx.beginPath();

        ctx.moveTo(vs[0].x + xOff + radius, vs[0].y);
        ctx.lineTo(vs[1].x - xOff - radius, vs[1].y);

        ctx.arcTo(vs[1].x - xOff, vs[1].y, vs[1].x - xOff, vs[1].y + radius, radius);
        ctx.lineTo(vs[2].x - xOff, vs[2].y - radius);

        ctx.arcTo(vs[2].x - xOff, vs[2].y, vs[2].x - xOff - radius, vs[2].y, radius);
        ctx.lineTo(vs[3].x + xOff + radius, vs[3].y);

        ctx.arcTo(vs[3].x + xOff, vs[3].y, vs[3].x + xOff, vs[3].y - radius, radius);
        ctx.lineTo(vs[0].x + xOff, vs[0].y + radius);
        ctx.arcTo(vs[0].x + xOff, vs[0].y, vs[0].x + xOff + radius, vs[0].y, radius);
        ctx.closePath();
        ctx.strokeStyle = '#494949'; // ; CBlockDrawingPreset.theme.mainMenuNormal; //  '#494949'; // s.strokeStyle;
        ctx.fillStyle = colBG;

        if (isShadow) {
            ctx.shadowBlur = 4;
            ctx.shadowColor = 'rgba(0,0,0,0.3)';
            ctx.shadowOffsetY = 2;
            ctx.shadowOffsetX = 2;
        } else {
            ctx.shadowBlur = 0;
            ctx.shadowOffsetY = 0;
            ctx.shadowOffsetX = 0;
        }

        ctx.fill();
        ctx.restore();
    }
    static RoundedRectFillWithCol(ctx: CanvasRenderingContext2D, vs: NVector3[], r: number, col: string = '#999999', xOff: number = 0, isShadow: boolean = true) {
        // let color = s.backgroundColor;
        let radius = r; // parseFloat(s.borderRadius);
        // console.log(color);
        // ctx.save();
        ctx.lineWidth = 2; // parseFloat(s.lineWidth);
        ctx.beginPath();

        ctx.moveTo(vs[0].x + xOff + radius, vs[0].y);
        ctx.lineTo(vs[1].x - xOff - radius, vs[1].y);

        ctx.arcTo(vs[1].x - xOff, vs[1].y, vs[1].x - xOff, vs[1].y + radius, radius);
        ctx.lineTo(vs[2].x - xOff, vs[2].y - radius);

        ctx.arcTo(vs[2].x - xOff, vs[2].y, vs[2].x - xOff - radius, vs[2].y, radius);
        ctx.lineTo(vs[3].x + xOff + radius, vs[3].y);

        ctx.arcTo(vs[3].x + xOff, vs[3].y, vs[3].x + xOff, vs[3].y - radius, radius);
        ctx.lineTo(vs[0].x + xOff, vs[0].y + radius);
        ctx.arcTo(vs[0].x + xOff, vs[0].y, vs[0].x + xOff + radius, vs[0].y, radius);
        ctx.closePath();
        ctx.strokeStyle = '#494949'; // s.strokeStyle;
        ctx.fillStyle = col;

        if (isShadow) {
            ctx.shadowBlur = 4;
            ctx.shadowColor = 'rgba(0,0,0,0.3)';
            ctx.shadowOffsetY = 2;
            ctx.shadowOffsetX = 2;
        } else {
            ctx.shadowBlur = 0;
            ctx.shadowOffsetY = 0;
            ctx.shadowOffsetX = 0;
        }

        ctx.fill();
        ctx.restore();
    }
    static SectionLine(ctx: CanvasRenderingContext2D, v: NVector3, width: number, offY: number) {
        ctx.strokeStyle = '#777777';
        ctx.lineWidth = 0.1;
        ctx.beginPath();
        ctx.moveTo(v.x, v.y + offY);
        ctx.lineTo(v.x + width, v.y + offY);
        ctx.closePath();
        ctx.stroke();
    }
    constructor() {/* */ }
}