import { MouseEventData } from '../../../../';

import { SubMenuCommonFly } from '../Menu/Common/SubMenuCommonFly';
import { PullDownMenuWrapper } from '../PullDownMenuWrapper';
import { MenuBase } from '../Menu/Common/Base/MenuBase';

// import { GraphicApp } from '../../../../index';
export class MenuRenderer {
    menus: MenuBase[] = [];
    capture: MenuBase | undefined;
    captureFlyout: SubMenuCommonFly | undefined;
    isShow: boolean = false;
    isActivated: boolean = false;
    isStillCapturedFly: boolean = true;

    pullDownMenuWrapper: PullDownMenuWrapper;

    constructor(pullDownMenuWrapper: PullDownMenuWrapper) {
        this.pullDownMenuWrapper = pullDownMenuWrapper;
        this.menus = [];
    }
    Add(m: MenuBase) {
        this.menus.push(m);
    }
    RemoveAll() {
        this.menus = [];
    }
    Remove(m: MenuBase) {
        this.menus.splice(this.menus.indexOf(m), 1);
    }
    Render(ctx: CanvasRenderingContext2D) {
        if (!this.isShow) { return; }
        for (let i = 0; i < this.menus.length; ++i) { this.menus[i].Render(ctx); }
    }
    set Capture(v: MenuBase | undefined) {
        if (v) {
            v.isCaptured = true;
            this.capture = v;
        } else {
            if (this.capture) {
                this.capture.isCaptured = false;
            }
        }
    }
    get Capture() {
        return this.capture;
    }
    ShowMenu(v: boolean) {
        this.isShow = v;
    }
    Reset() {
        for (let i = 0; i < this.menus.length; ++i) {
            this.menus[i].IsHover = false;
        }
        this.isActivated = false;
        // this.pullDownMenu.stopPropagation22 = true;
    }
    MouseMove(e: MouseEventData) {
        if (!this.isShow || !this.isActivated) { return; }
        // if (PullDownMenuWrapper.pullDownMenuWrapper) {
        //     if (!PullDownMenuWrapper.pullDownMenuWrapper.isActivated) { return; }
        // }
        let num = 0;
        for (let i = 0; i < this.menus.length; ++i) {
            if (this.menus[i].IsMouseInside(e.x, e.y)) {
                this.menus[i].IsHover = true;
                // this.pullDownMenu.nUI.pullDownMenuWrapper.stoppropagation = false;
                num++;
            }
        }
        // if (!isStillCapturedFly) {
        if (num !== 0) {
            for (let i = 0; i < this.menus.length; ++i) {
                if (this.menus[i].IsMouseInside(e.x, e.y)) {
                    this.menus[i].IsHover = true;
                } else {
                    this.menus[i].IsHover = false;
                }
            }
        }
        // }
    }
    MouseDown(e: MouseEventData) {
        if (!this.isShow) { return; }
        if (!this.isActivated) { this.isActivated = true; }

        for (let i = 0; i < this.menus.length; ++i) {
            if (this.menus[i].isHover) {
                this.menus[i].MouseDown();
                this.ClickForStopPropagation();
            }
        }

        let num = 0;
        for (let i = 0; i < this.menus.length; ++i) {
            if (this.menus[i].IsMouseInside(e.x, e.y)) {
                num++;
            }
        }

        if (num === 0) {
            this.Reset();
        } else {
            for (let i = 0; i < this.menus.length; ++i) {
                if (this.menus[i].IsMouseInside(e.x, e.y)) {
                    this.menus[i].IsHover = true;
                } else {
                    this.menus[i].IsHover = false;
                }
            }
        }
    }
    ClickForStopPropagation() {
        this.pullDownMenuWrapper.stopPropagation = true;
    }
}