import { NVector3 } from '../../../../../../';
import { MouseEventData } from '../../../../../../IO/EventInteraction/MouseInteraction';

import { IndePanelBase } from './base/IndePanelBase';

export class BtnResize extends IndePanelBase {

    host: IndePanelBase;

    constructor(cUI: IndePanelBase, x: number, y: number, width: number, height: number) {
        super(cUI.independentPanelWrapper);
        this.title = '/';
        this.vec = new NVector3(x, y);
        this.host = cUI;
        this.width = width;
        this.height = height;
    }
    MouseDown() {
        this.independentPanelWrapper.stopPropagation = true;
        //  this.host.isMinimized = !this.host.isMinimized;
    }
    Translate(e: MouseEventData): void {
        if (!this.isMoveable) { return; }
        let xo = e.x - e.preX;
        let yo = e.y - e.preY;

        let xx = this.host.width + xo;
        let yy = this.host.height + yo;
        if (xx < this.host.minWidth) { xo = 0; }
        if (yy < this.host.minHeight) { yo = 0; }
        this.host.Width += xo;
        this.host.Height += yo;
        this.ResizePropagation(this.host.Width, this.host.Height);
        this.vec.x += xo;
        this.vec.y += yo;
        for (let i = 0; i < this.boundarVecs.length; ++i) { this.boundarVecs[i].x += xo; this.boundarVecs[i].y += yo; }
    }
    ResizePropagation(width: number, height: number) {
        // console.log('ResizePropagation', width,  height);
    }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        if (this.isHidden) { return; }
        // this.ComputeBoundary();
        // IndePanelPreset.RoundedRectFillWithColorMainMenu(ctx, this.title, this.boundarVecs, this.filletR, 0, this.isOver, this.isSelected, '#ff0000', false);

        ctx.save();
        if (this.isOver) {
            ctx.strokeStyle = 'black';
        } else {
            ctx.strokeStyle = 'gray';
        }
        ctx.beginPath();
        ctx.moveTo(this.vec.x + this.width - 5, this.vec.y);
        ctx.lineTo(this.vec.x, this.vec.y + this.height - 5);
        ctx.stroke();
        ctx.restore();

    }

}