import { NVector3, NumericalMotion } from '../../../../../';
import { PullDownMenuWrapper } from '../../PullDownMenuWrapper';
import { SubMenuIconCommon } from './SubMenuIconCommon';
import { SubMenuCommonFly } from './SubMenuCommonFly';
// import { MenuBase } from './Base/MenuBase';
// import {SubMenuIconCommon} from './SubMenuIconCommon';
import { MenuPreset } from '../../Core/MenuPreset';
export class SubMenuIconFlyCommon extends SubMenuIconCommon {

    pullDownMenuWrapper: PullDownMenuWrapper;

    title: string;
    menus: SubMenuIconCommon[] = [];

    heightSub: number;
    widthSub: number;

    vecSub: NVector3;

    isClickable: boolean = true;

    constructor(menu: SubMenuCommonFly, title: string, yOff: number) {
        super(menu.menu, title, yOff);
        this.vec = new NVector3(this.menu.vec.x + this.menu.pullDownMenuWrapper.theme.widthEach, yOff);
        this.height = this.menu.pullDownMenuWrapper.theme.heightEach;
        this.width = this.menu.pullDownMenuWrapper.theme.widthEach;
    }
    MouseDown() {
        if (!this.isClickable) { return; }
        this.MouseDownPost();
    }
    MouseDownPost() {
        /* */
    }
    get IsHover() {
        return this.isHover;
    }
    set IsHover(v: boolean) {
        this.isHover = v;

        for (let i = 0; i < this.menus.length; ++i) {
            this.menus[i].isHide = !v;
        }

    }
    RenderPost(ctx: CanvasRenderingContext2D): void {

        this.ComputeBoundary();
        // console.log(this);
        if (this.MouseInside(this.menu.pullDownMenuWrapper.mouse.x, this.menu.pullDownMenuWrapper.mouse.y)) {
            this.isHover = true;
            this.t += this.menu.pullDownMenuWrapper.theme.animationSpeed;
            // MenuRenderer.captureFlyout = this;
        } else {
            this.t = 0;
            // if (MenuRenderer.captureFlyout === undefined) {
            this.isHover = false;
            // }
        }
        if (this.isClickable) {
            if (this.isHover) {
                if (this.t > 1) { this.t = 1.0; }
                MenuPreset.RoundedRectFill(ctx, this.menu.pullDownMenuWrapper.theme.colSelectedBG, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width * NumericalMotion.EaseOutQuad(this.t), this.height), 0);
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colTextHover;
            } else {
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colText;
            }
        } else {
            ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colTextDisable;
        }
        ctx.shadowBlur = 0;
        ctx.shadowOffsetY = 0;
        ctx.shadowOffsetX = 0;
        ctx.font = this.menu.pullDownMenuWrapper.theme.font;
        // ctx.textAlign = this.menu.pullDownMenuWrapper.theme.textAlign;
        ctx.fillText(this.title, this.vec.x + this.menu.pullDownMenuWrapper.textOffX, this.vec.y + this.menu.pullDownMenuWrapper.textOffY);

        if (this.isHover) {
            // console.log(this);
            // MenuPreset.RoundedRectFillWithCol(ctx, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width, this.height), 3, this.menu.pullDownMenuWrapper.theme.colBG);

        }
    }
}