import { NVector3 } from '../../../../..';

import { PullDownMenuWrapper } from '../../PullDownMenuWrapper';

import { MenuCommon } from '../Common/MenuCommon';
import { SubMenuCommonSection } from '../Common/SubMenuCommonSection';
import { SubMenuIconCommon } from '../Common/SubMenuIconCommon';
import { SubMenuIconFlyCommon } from '../Common/SubMenuIconFlyCommon';
import { SubMenuIconFlyCommonSection } from '../Common/SubMenuIconFlyCommonSection';
import { SubMenuCommonFly } from '../Common/SubMenuCommonFly';

export class MenuNDrawingBlock extends MenuCommon {

    pullDownMenuWrapper: PullDownMenuWrapper;

    title: string;

    // tslint:disable-next-line:no-any
    ac: any;

    // tslint:disable-next-line:no-any
    constructor(pullDownMenuWrapper: PullDownMenuWrapper, ac: any, title: string, xOff: number) {
        super(pullDownMenuWrapper, ac);
        this.pullDownMenuWrapper = pullDownMenuWrapper;
        this.vec = new NVector3(this.pullDownMenuWrapper.vec.x + xOff, this.pullDownMenuWrapper.vec.y);
        this.height = this.pullDownMenuWrapper.height;
        this.title = title;
        this.width = (this.pullDownMenuWrapper.textOffX * 2) + (this.title.length * 6);
        this.Init();
    }
    Init() {
        this.AppendCommand();
        this.heightSub = this.pullDownMenuWrapper.theme.heightEach * this.menus.length;
        this.widthSub = this.pullDownMenuWrapper.theme.widthEach;
        this.vecSub = new NVector3(this.vec.x, this.vec.y + this.pullDownMenuWrapper.bar.height + 1);
    }
    AppendCommand() {
        // const ac = this.pullDownMenuWrapper.viewUIWrapper.viewController.model.controllerCoreWrapper.activeCommandController;
        let numInterval = 0, numSubInterval = 0;
        let m = new SubMenuIconCommon(this, 'dummy', this.pullDownMenuWrapper.bar.height);
        // .............................................................................................................................. sub flyout menu
        m = new SubMenuIconCommon(this, 'About Drawing Block', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        // m.isClickable = false;
        m.MouseDownPost = () => {
            this.pullDownMenuWrapper.nUI.independentPanelWrapper.activeCommand.AboutAnnotation();
            // if (this.pullDownMenuWrapper.viewController.njslabSandbox.viewController) { this.pullDownMenuWrapper.viewController.njslabSandbox.viewController.indePanel.activeCommand.AboutDrawingTool(); }
            // this.pullDownMenuWrapper.viewController.njslabSandbox.ACController.Stoppropagation = true; this.pullDownMenuWrapper.Reset();
            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        m = new SubMenuIconCommon(this, 'Load Creative Coding Environment ', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.MouseDownPost = () => {
            this.ac('creativeCoding');
            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        // .............................................................................................................................. sub flyout menu
        m = new SubMenuCommonFly(this, 'Definition', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);

        let mFly = m as SubMenuCommonFly; numSubInterval = 0;
        let subM = new SubMenuIconFlyCommon(mFly, 'Blank', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.MouseDownPost = () => {
            this.ac('creativeCoding@Blank');
        };
        m.menus.push(subM);

        // ...............................................................................
        m.menus.push(new SubMenuIconFlyCommonSection(mFly, 'section', m.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++));
        subM = new SubMenuIconFlyCommon(mFly, 'BallBounce', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.MouseDownPost = () => {
            this.ac('creativeCoding@BallBounce');
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'Line2', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.MouseDownPost = () => {

            this.ac('creativeCoding@Line2');
        };
        m.menus.push(subM);

        // ...............................................................................
        m.menus.push(new SubMenuIconFlyCommonSection(mFly, 'section', m.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++));
        subM = new SubMenuIconFlyCommon(mFly, 'Boid System 01', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.MouseDownPost = () => {
            this.ac('creativeCoding@BoidSystem01');
        };
        m.menus.push(subM);

        m.menus.push(new SubMenuIconFlyCommonSection(mFly, 'section', m.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++));

        subM = new SubMenuIconFlyCommon(mFly, 'OOP 01', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.MouseDownPost = () => {

            this.ac('creativeCoding@OOP01');
        };
        m.menus.push(subM);
        this.menus.push(m);

        // ................................................................
        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        m = new SubMenuIconCommon(this, 'Option', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {

            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);
    }
}