import { NVector3 } from '../../../../../';
import { SubMenuIconCommon } from './SubMenuIconCommon';
import { MenuCommon } from './MenuCommon';
import { MenuPreset } from '../../Core/MenuPreset';
export class SubMenuCommonSection extends SubMenuIconCommon {
    vec: NVector3;
    boundarVecs: NVector3[];
    // menu: MenuCommon;
    title: string;
    height: number;
    width: number;
    isHide: boolean;
    isHover: boolean;
    offY: number;
    constructor(menu: MenuCommon, title: string, yOff: number) {
        super(menu, title, yOff);
        this.menu = menu;
        this.title = title;
        this.vec = new NVector3(this.menu.vec.x, this.menu.vec.y + yOff);
        this.height = this.menu.pullDownMenuWrapper.theme.heightEach;
        this.width = this.menu.pullDownMenuWrapper.theme.widthEach;
        this.offY = this.height * 0.5;
    }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        if (this.isHide) { return; }
        MenuPreset.SectionLine(ctx, this.vec, this.width, this.offY);
    }
}