import { NVector3 } from '../../../../../../';

import { IndependentPanel } from '../IndependentPanel';

import { IndePanelPreset } from '../../../Core/IndePanelPreset';

import { IndeSubMenu } from './IndeSubMenu';

import { ControllerTheme } from '../../../../Core/ControllerTheme';

export class IndeSubPanel {
    list: IndeSubMenu[] = [];
    independentPanel: IndependentPanel;
    width: number;
    height: number;
    textHeight: number = 20;
    vec: NVector3;

    xOff: number = -5;
    yOff: number = 10;
    isHidden: boolean = true;
    theme: ControllerTheme;
    constructor(independentPanel: IndependentPanel) {
        this.independentPanel = independentPanel;
        this.theme = this.independentPanel.theme;
        this.vec = new NVector3(this.independentPanel.vec.x + this.independentPanel.width + this.xOff, this.independentPanel.vec.y + this.yOff);
        this.width = 150;
        this.height = 100;
    }
    Add(v: string) {
        this.list.push(new IndeSubMenu(this, this.list.length, v));
    }
    get IsHidden() {
        return this.isHidden;
    }
    set IsHidden(v: boolean) {
        this.isHidden = v;
        for (let i = 0; i < this.list.length; ++i) {
            this.list[i].IsHidden = v;
        }
    }
    Remove() {
        for (let i = 0; i < this.list.length; ++i) {
            this.list[i].Remove();
        }
    }
    Translate(xo: number, yo: number) {
        // if (this.isHidden) { return; }
        this.vec.x += xo; this.vec.y += yo;
        for (let i = 0; i < this.list.length; ++i) {
            this.list[i].TranslateByXY(xo, yo);
        }
    }
    RenderPost(ctx: CanvasRenderingContext2D) {
        if (this.isHidden) { return; }
        this.height = this.list.length * this.textHeight + 10;
        let fillCol =  this.independentPanel.theme.colSelectedBG;
        IndePanelPreset.RoundedRectFill(ctx, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width, this.height), 3, 0, fillCol);

        for (let i = 0; i < this.list.length; ++i) {
            this.list[i].RenderPost(ctx);
        }
    }
}