export enum UNIT_LENGTH {
    METER = 'METER', KILOMETER = 'KILOMETER', CENTIMETER = 'CENTIMETER', MILLIMETER = 'MILLIMETER', MICROMETER = 'MICROMETER', NANOMETER = 'NANOMETER',
    MILE = 'MILE', YARD = 'YARD', FOOT = 'FOOT', INCH = 'INCH'
}
export enum UNIT {
    METER = 'METER',
    KM = 'KM',
    MILE = 'MILE'
}
export const shuffle = (array: any) => {
    var currentIndex = array.length, temporaryValue, randomIndex;
    while (0 !== currentIndex) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
    return array;
};
export const random = (seed: number) => {
  var mask = 0xffffffff;
  var m_w  = (123456789 + seed) & mask;
  var m_z  = (987654321 - seed) & mask;

  return function() {
    m_z = (36969 * (m_z & 65535) + (m_z >>> 16)) & mask;
    m_w = (18000 * (m_w & 65535) + (m_w >>> 16)) & mask;

    var result = ((m_z << 16) + (m_w & 65535)) >>> 0;
    result /= 4294967296;
    return result;
  };
};
