    public static Series(s: number = 0, e: number = 10, step: number = 1) {
        let series: number[] = [];
        for (let i = 0; i < e; i += step) { series.push(i); }
        return series;
    }
    public static Range(s: number = 0, e: number = 1, rNumber: number = 10) {
        let range: number[] = [];
        let theStep = e / rNumber;
        for (let i = 0; i < e; i += theStep) { range.push(i); }
        return range;
    }
