    // region get ReMap Nomalize From 0.0 to 1.0
    public static ReMapNomalize(CValue: number[]) {
        let valueOut: number[] = [];
        let domain: number[] = MathUtility.GetDomain(CValue);
        for (let d of CValue) { valueOut.push(((d - domain[0]) / (domain[1] - domain[0]))); }
        return valueOut;
    }
