    // region get re-map
    public static Remaps(CValue: number[], OldMin: number, OldMax: number, NewMin: number, NewMax: number) {
        let temp: number[] = [];
        for (let d of CValue) { temp.push(MathUtility.Remap(d, OldMin, OldMax, NewMin, NewMax)); }
        return temp;
    }
    public static Remap(CValue: number, OldMin: number, OldMax: number, NewMin: number, NewMax: number) {
        return (((CValue - OldMin) * (NewMax - NewMin)) / (OldMax - OldMin)) + NewMin;
    }
    public static GetDomain(doubleList: number[]) {
        let Min: number = Number.MAX_VALUE; let Max: number = Number.MIN_VALUE;
        for (let d of doubleList) { if (Min > d) { Min = d; } if (Max < d) { Max = d; } }
        return [Min, Max];
    }
    public static GetIndexBySort(d: number[]) {
        let len = d.length;
        let indices = new Array(len);
        for (var i = 0; i < len; ++i) { indices[i] = i; }
        indices.sort((a, b) => { return d[a] < d[b] ? -1 : d[a] > d[b] ? 1 : 0; });
        return indices;
    }
    public static GetMaxIndex(d: number[]) {
        let index = -1;
        let max = Number.MIN_SAFE_INTEGER;
        for (let i = 0; i < d.length; ++i) {
            if (max < d[i]) { max = d[i]; index = i; }
        }
        return index;
    }
