    public static RemapMeterFromGPSPos(lat1: number, lon1: number, lat2: number, lon2: number) {  // generally used geo measurement function
        let R = 6378.137; // Radius of earth in KM
        let dLat = lat2 * Math.PI / 180 - lat1 * Math.PI / 180;
        let dLon = lon2 * Math.PI / 180 - lon1 * Math.PI / 180;
        let a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
            Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
            Math.sin(dLon / 2) * Math.sin(dLon / 2);
        let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
        let d = R * c;
        return d * 1000; // meters
    }
    public static GetKilogramFromNewton(w: number): number { return w * 0.10197162129779283; }
    public static GetNewtonFromKilogram(w: number): number { return w * 9.80665; }
