    public static ConverterUnitLength(value: number, from: UNIT_LENGTH, to: UNIT_LENGTH) {
        // MathUtility.ConverterUnitLengthFromMeter
        switch (from) {
            case UNIT_LENGTH.NANOMETER: return value * 1000000000;
            case UNIT_LENGTH.MICROMETER: return value * 1000000;
            case UNIT_LENGTH.MILLIMETER: return value * 1000;
            case UNIT_LENGTH.CENTIMETER: return value * 100;
            case UNIT_LENGTH.METER: return value;
            case UNIT_LENGTH.INCH: return value * 39.37007874;
            case UNIT_LENGTH.FOOT: return value * 3.280839895;
            case UNIT_LENGTH.YARD: return value * 1.0936132983;
            case UNIT_LENGTH.MILE: return value * 0.0006213689;
            case UNIT_LENGTH.KILOMETER: return value * 0.001;
            default: return value;
        }
    }
    public static ConverterUnitLengthFromMeter(value: number, unitLength: UNIT_LENGTH) {
        switch (unitLength) {
            case UNIT_LENGTH.NANOMETER: return value * 1000000000;
            case UNIT_LENGTH.MICROMETER: return value * 1000000;
            case UNIT_LENGTH.MILLIMETER: return value * 1000;
            case UNIT_LENGTH.CENTIMETER: return value * 100;
            case UNIT_LENGTH.METER: return value;
            case UNIT_LENGTH.INCH: return value * 39.37007874;
            case UNIT_LENGTH.FOOT: return value * 3.280839895;
            case UNIT_LENGTH.YARD: return value * 1.0936132983;
            case UNIT_LENGTH.MILE: return value * 0.0006213689;
            case UNIT_LENGTH.KILOMETER: return value * 0.001;
            default: return value;
        }
    }

