    public static Clamp(v: number, min: number, max: number) { return Math.max(min, Math.min(max, v)); }
    public static SmoothStep(edge1: number, edge2: number, val: number) { // https://en.wikipedia.org/wiki/Smoothstep
        var x = Math.max(0, Math.min(1, (val - edge1) / (edge2 - edge1)));
        return x * x * (3 - 2 * x);
    }
    // Get a number between A and B from a normalized number.
    public static Lerp(a: number, b: number, t: number) { return a + t * (b - a); }

    // From a point in space, closest spot to a 2D line
    public static ClosestPointToLine2D(x0: number, y0: number, x1: number, y1: number, px: number, py: number) {
        var dx = x1 - x0,
            dy = y1 - y0,
            t = ((px - x0) * dx + (py - y0) * dy) / (dx * dx + dy * dy),
            x = x0 + (dx * t), // Util.lerp(x0, x1, t),
            y = y0 + (dy * t); // Util.lerp(y0, y1, t);
        return [x, y];
    }

