    // http://hevi.info/do-it-yourself/interpolating-and-array-to-fit-another-size/
    public static LinearInterpolate(before: number, after: number, atPoint: number) {
        return before + (after - before) * atPoint;
    }
    public static GetInterpolatedArray(data: number[], fitCount: number) {
        let newData = {};
        let springFactor = (data.length - 1) / (fitCount - 1);
        newData[0] = data[0]; // for new allocation
        for (let i = 1; i < fitCount - 1; i++) {
            let tmp: number = i * springFactor;
            let before: number = +(Math.floor(tmp)).toFixed();
            let after: number = +(Math.ceil(tmp)).toFixed();
            let atPoint: number = tmp - before;
            newData[i] = this.LinearInterpolate(data[before], data[after], atPoint);
        }
        newData[fitCount - 1] = data[data.length - 1]; // for new allocation
        return newData;
    }
