    public static RemapLongToSurface(CValue: number, min: number, max: number) { return (((CValue - (-180)) * (max - min)) / (180 - (-180)) + min); }
    public static RemapLatToSurface(CValue: number, min: number, max: number) { return (((CValue - (-90)) * (max - min)) / (90 - (-90)) + min); }
    public static RemapLongToSurfaceNormal(CValue: number) { return ((CValue - (-180)) * (1.0 - 0)) / (180 - (-180)); } // 0 to 1
    public static RemapLatToSurfaceNormal(CValue: number) { return ((CValue - (-90)) * (1.0 - 0)) / (90 - (-90)); } // 0 to 1
    public static RemapLong(CValue: number) { return (CValue + 180) * Math.PI * 2 / 360 * -1; } // -1 for reversing direction
    public static RemapLat(CValue: number) { return (CValue + 90) * Math.PI / 180; }
