export class ControllerTheme {

    static ThemePulldown = {
        colBG: '#ffffff',

        colSection: '#777777',
        colText: '#333333',
        colTextHover: '#ffffff',
        colTextDisable: '#cccccc',

        colSelectedBG: '#ff0000',
        colSelectedText: '#ffffff',

        widthEach: 250,
        heightEach: 20,

        textYOff: 15,
        font: 'normal 11px arial',

        animationSpeed: 0.195,

        textAlign: 'left',

        toolbarBG: '#cccccc',
        toolbarFG: '#ffffff'
    };

    colBG: string = '#fffffe';
    colSection: string = '#777777';
    colText: string = '#333333';
    colTextHover: string = '#ffffff';
    colTextDisable: string = '#bbbbbb';

    colSelectedBG: string = '#ff0000';
    colSelectedText: string = '#ffffff';

    widthEach: number = 230;
    heightEach: number = 22;

    textYOff: number = 20 - 5;
    font: string = 'normal 11px arial'; // 100 Sans-serif //
    textAlign: string = 'start';

    animationSpeed: number = 0.138;

    toolbarBG: string = '#cccccc';
    toolbarFG: string = '#ffffff';

    textBaseline: string = 'alphabetic';

    constructor() {
        // let json = JSON.parse(PulldownMenuStyle.ThemePulldown);
    }
    // tslint:disable-next-line:no-any
    ShiftTheme(v: any) {
        this.colBG = v.colBG;
        this.colSection = v.colSection;
        this.colText = v.colText;
        this.colTextHover = v.colTextHover;
        this.colTextDisable = v.colTextDisable;

        this.colSelectedBG = v.colSelectedBG;
        this.colSelectedText = v.colSelectedText;

        this.widthEach = v.widthEach;
        this.heightEach = v.heightEach;

        this.textYOff = v.textYOff;
        this.font = v.font;
        this.textAlign = v.textAlign;

        this.toolbarBG = v.toolbarBG;
        this.toolbarFG = v.toolbarFG;
    }
}