import { NVector3 } from '../../../../../../';
import { IndePanelBase } from './base/IndePanelBase';

export class BtnClose extends IndePanelBase {

    host: IndePanelBase;

    constructor(indePanelBase: IndePanelBase, x: number, y: number) {
        super(indePanelBase.independentPanelWrapper);
        this.title = 'X';
        this.vec = new NVector3(x, y);
        this.host = indePanelBase;
        this.width = indePanelBase.HeadHeight;
        this.height = indePanelBase.HeadHeight;
    }
    MouseDown() {
        this.independentPanelWrapper.stopPropagation = true;
        this.host.Remove();

    }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        // this.ComputeBoundary();
        // IndePanelPreset.RoundedRectFillWithColorMainMenu(ctx, this.title, this.boundarVecs, this.filletR, 0, this.isOver, this.isSelected, '#ff0000', false);
        this.RenderTitle(ctx);

    }
    RenderTitle(ctx: CanvasRenderingContext2D) {
        ctx.save();
        if (this.isOver) {
            ctx.fillStyle = 'black';
        } else {
            ctx.fillStyle = 'white';
        }
        ctx.textBaseline = 'top';
        ctx.textAlign = 'center';
        ctx.font = '300 17px Sans-serif';
        ctx.fillText(this.title, this.vec.x + (this.width * 0.5) - 3, this.vec.y + (this.height * 0.5) - 8);

        ctx.shadowBlur = 0;
        ctx.shadowOffsetY = 0;
        ctx.shadowOffsetX = 0;
        ctx.restore();
    }
}