import { NVector3 } from '../../../../../';
import { PullDownMenuWrapper } from '../../PullDownMenuWrapper';
import { MenuBase } from './Base/MenuBase';
import { SubMenuIconCommon } from './SubMenuIconCommon';
import { MenuPreset } from '../../Core/MenuPreset';
// import { MenuRenderer } from '../../Core/MenuRenderer';
export class MenuCommon extends MenuBase {
    title: string;
    menus: SubMenuIconCommon[] = [];

    heightSub: number;
    widthSub: number;

    // tslint:disable-next-line:no-any
    ac: any;

    isClick: boolean = false;
    // tslint:disable-next-line:no-any
    constructor(pullDownMenuWrapper: PullDownMenuWrapper, ac: any) {
        super(pullDownMenuWrapper);
        this.ac = ac;
    }
    public AddApp(v: string) {
        //
    }
    public MouseDown() {
        for (let i = 0; i < this.menus.length; ++i) {
            if (this.menus[i].isHover) {
                this.menus[i].MouseDown();
            }
        }
    }
    public get IsHover() {
        return this.isHover;
    }
    public set IsHover(v: boolean) {
        this.isHover = v;

        for (let i = 0; i < this.menus.length; ++i) {
            this.menus[i].isHide = !v;
        }

    }
    public IsMouseInside(x: number, y: number) {
        return NVector3.IsInsideOfVecs(new NVector3(x, y), this.ComputeBoundary());
    }
    public RenderPost(ctx: CanvasRenderingContext2D): void {

        this.ComputeBoundary();

        if (this.isHover) {
            MenuPreset.RoundedRectFill(ctx, this.pullDownMenuWrapper.theme.colSelectedBG, this.boundarVecs, 0);
            ctx.fillStyle = this.pullDownMenuWrapper.theme.colTextHover;
            //  MenuRenderer.captureFlyout = undefined;
        } else {
            ctx.fillStyle = this.pullDownMenuWrapper.theme.colText;
        }
        ctx.shadowBlur = 0;
        ctx.shadowOffsetY = 0;
        ctx.shadowOffsetX = 0;
        // ctx.textBaseline =  this.pullDownMenuWrapper.theme.textBaseline as CanvasTextBaseline; // 'middle';
        // ctx.textAlign = this.pullDownMenuWrapper.theme.textAlign as CanvasTextAlign;
        ctx.font = this.pullDownMenuWrapper.theme.font;
        ctx.fillText(this.title, this.vec.x + this.pullDownMenuWrapper.textOffX, this.vec.y + this.pullDownMenuWrapper.textOffY);

        if (this.isHover) {
            if (!this.isClick) {
                this.ClickForStopPropagation();
                this.isClick = true;
            }

            // console.log(this);
            MenuPreset.RoundedRectFillWithCol(ctx, NVector3.GetBoundaryByVecWidthHeight(this.vecSub, this.widthSub, this.heightSub), 3, this.pullDownMenuWrapper.theme.colBG);
            for (let i = 0; i < this.menus.length; ++i) {
                this.menus[i].RenderPost(ctx);
            }
        } else {
            this.isClick = false;
        }
    }
    public ClickForStopPropagation() {
        this.pullDownMenuWrapper.stopPropagation = true;
    }
    public AddMenu(subMenu: string, title: string, fn: () => void) {
        //
    }
}