
import { MouseEventData } from '../../../../';
import { IndePanelBase } from '../Panel/Common/base/base/IndePanelBase';
import { IndependentPanelWrapper } from '../IndependentPanelWrapper';
export class IndePanelRender {
    list: IndePanelBase[] = [];

    isShow: boolean = false;
    isStillCapturedFly: boolean = true;

    independentPanelWrapper: IndependentPanelWrapper;
    isHidden: boolean = false;
    capture: IndePanelBase | undefined = undefined;

    constructor(independentPanelWrapper: IndependentPanelWrapper) {
        this.independentPanelWrapper = independentPanelWrapper;
    }
    get IsHidden() { return this.isHidden; }
    set IsHidden(v: boolean) { this.isHidden = v; }
    RemoveAllCapturedBlock() {
        if (this.capture === undefined) { return; }
        this.capture.Remove();
    }
    CheckOvered(e: MouseEventData) {
        let i = this.list.length;
        let mouseHoverUI = false;
        while (i--) {
            if (this.list[i].isHidden) { continue; }
            if (this.list[i].IsMouseInside(e.x, e.y)) {
                this.list[i].IsHover = true;
                mouseHoverUI = true;
            } else {
                this.list[i].IsHover = false;
            }
        }
        if (mouseHoverUI) {
            // this.independentPanelWrapper.stoppropagation = true;
        } else {
            // this.independentPanelWrapper.stoppropagation = false;
        }
    }
    FindAtPoint(e: MouseEventData) {
        let i = this.list.length;
        while (i--) {
            if (this.list[i].isHidden) { continue; }
            if (this.list[i].IsMouseInside(e.x, e.y)) {
                return this.list[i];
            }
        }
        return undefined;
    }
    MouseDown(e: MouseEventData) {
        if (this.isHidden) { return; }
        let ui = this.FindAtPoint(e);
        if (ui !== undefined) {
            this.independentPanelWrapper.enablePan = false;
            ui.MouseDown();
            this.capture = ui;
        } else {
            this.capture = undefined;
        }
    }
    MouseRight(e: MouseEventData) {
        if (this.isHidden) { return; }
        let ui = this.FindAtPoint(e);
        if (ui !== undefined) {
            this.independentPanelWrapper.enablePan = false;
            ui.MouseRight(e);
            this.capture = ui;
        } else {
            this.capture = undefined;
        }
    }
    MouseDrag(e: MouseEventData) {
        if (this.isHidden) { return; }
        if (this.capture !== undefined) {
            // this.independentPanelWrapper.stoppropagation = true;
            this.capture.MouseDrag(e);
        }
    }
    MouseDouble(e: MouseEventData) {
        if (this.isHidden) { return; }
        let ui = this.FindAtPoint(e);
        if (ui !== undefined) {
            this.independentPanelWrapper.enablePan = false;
            ui.MouseDouble(e);
            // this.capture = ui;
        }
        // if (this.capture !== undefined) { }
    }
    MouseMove(e: MouseEventData) {
        if (this.isHidden) { return; }
        this.CheckOvered(e);
        this.FindAtPoint(e);
    }
    MouseUp(e: MouseEventData) {
        if (this.isHidden) { return; }
        if (this.capture !== undefined) {
            this.capture.MouseUp(e);
            // this.capture = undefined;
        }
        // this.independentPanelWrapper.stoppropagation = false;
        this.independentPanelWrapper.enablePan = true;
    }
    Render(ctx: CanvasRenderingContext2D) {
        if (this.isHidden) { return; }
        for (let i = 0; i < this.list.length; ++i) {
            this.list[i].Render(ctx);
        }
        // ToolbarRender.Render(ctx);
        // MenuRenderer.Render(ctx);
        // console.log(IndePanelRender.list.length);
    }
    // .........................................................
    Add(g: IndePanelBase) {
        this.list.push(g);
    }
    Remove(g: IndePanelBase) {
        this.list.splice(this.list.indexOf(g), 1);
    }
    RemoveAll() {
        this.list = [];
    }

    ShowMenu(v: boolean) {
        this.isShow = v;
    }
    Reset() {
        for (let i = 0; i < this.list.length; ++i) {
            this.list[i].IsHover = false;
        }
    }
    // MouseMove(e: MouseEventData) {
    //     // console.log(this.list);
    //     if (!this.isShow) { return; }
    //     for (let i = 0; i < this.list.length; ++i) {
    //         if (this.list[i].IsMouseInside(e.x, e.y)) {
    //             this.list[i].IsHover = true;
    //         } else {
    //             this.list[i].IsHover = false;
    //         }
    //     }
    // }
    // MouseDown(e: MouseEventData) {
    //     if (!this.isShow) { return; }
    //     this.MouseMove(e);

    //     for (let i = 0; i < this.list.length; ++i) {
    //         if (this.list[i].IsHover) {
    //             this.list[i].MouseDown();
    //         }
    //     }
    // }
}