import { NVector3 } from '../../../';

import { PixelMap } from './PixelMap';
import { VoxelMap } from './VoxelMap';
import { MGridHex } from './MGridHex';
export class Pixel {
    static ID = 0;

    map: PixelMap | VoxelMap | MGridHex;
    vec: NVector3;
    norV: NVector3;
    nPix: Pixel[] = [];
    recVec: NVector3[];
    i: number = -1;
    j: number = -1;
    k: number = -1;
    u: number = -1;
    v: number = -1;
    id: number;
    data: {};

    static GetID() { return Pixel.ID++; }

    constructor(map: PixelMap | VoxelMap | MGridHex, v: NVector3 = new NVector3(0, 0, 0)) {
        this.map = map;
        this.vec = v;
        this.nPix = [];
        this.recVec = [];
        this.i = -1;
        this.j = -1;
        this.k = -1;
        this.id = Pixel.GetID();
        this.data = {};
    }
    ToString() {
        let temp = 'Pixel' + this.id;
        console.log(temp);
        return temp;
    }
    Update() {
        /* */
    }
    Render() {
        /* */
    }
}
