export class ArrayUtility {
  public static FindIndexOfMax(array: number[]) {
    let i: number = 1;
    let mi: number = 0;
    while (i < array.length) {
      if (!(array[i] < array[mi])) { mi = i; }
      i += 1;
    }
    return mi;
  }
  public static FindIndexOfMin(array: number[]) {
    let i: number = 1;
    let mi: number = 0;
    while (i < array.length) {
      if (!(array[i] > array[mi])) { mi = i; }
      i += 1;
    }
    return mi;
  }
}
export class Print {
  public static isPrint: boolean = true;
  public static log(s: string) {
    if (!Print.isPrint) { return; }
    // console.log(s);
  }
}