export class CanvasLoopController {
    numStopFrame: number;
    private _level: number = -1;
    private _stop: boolean = false;
    private numRender: number = 0;
    constructor(numStopFrame: number) {
        this.numStopFrame = numStopFrame;
    }
    public get stop() {
        return this._stop;
    }
    public set stop(v: boolean) {
        this.numRender = 0;
        this._stop = v;
    }
    public TimerForStop() {
        this.numRender++;
        // console.log(this.numRender);
        if (this.numRender > this.numStopFrame) { this.numRender = 0; this.stop = true; }
    }
    public Reset() {
        this._level = -1;
    }
    public get level() {
        return this._level;
    }
    public set level(v: number) {
        this._level = v;
    }
}