import { NVector3 } from '../../../../../../';
import { MouseEventData } from '../../../../../../IO/EventInteraction/MouseInteraction';
import { IndePanelBase } from './base/IndePanelBase';
export class BtnHam extends IndePanelBase {

    host: IndePanelBase;

    constructor(indePanelBase: IndePanelBase, x: number, y: number) {
        super(indePanelBase.independentPanelWrapper);
        this.title = '-';
        this.vec = new NVector3(x, y);
        this.host = indePanelBase;
        this.width = 20;
        this.height = 20;
    }
    MouseDown() {
        this.independentPanelWrapper.stopPropagation = true;
        this.host.HasSubMenu = !this.host.HasSubMenu;
    }
    MouseDrag(e: MouseEventData) { /* */ }
    Move(x: number, y: number) { /* */ }
    // Translate(e: MouseEventData): void {
    //     /* */
    // }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        // this.ComputeBoundary();
        // IndePanelPreset.RoundedRectFillWithColorMainMenu(ctx, this.title, this.boundarVecs, this.filletR, 0, this.isOver, this.isSelected, '#ff0000', false);

        ctx.save();
        if (this.isOver) {
            ctx.strokeStyle = 'black';
        } else {
            ctx.strokeStyle = 'white';
        }
        ctx.beginPath();
        ctx.lineWidth = 2;
        // if (this.host.isMinimized) {
        //     ctx.rect(this.vec.x, this.vec.y + this.host.HeadHeight * 0.5 - 5, 10, 10);
        // } else {

        const theHeight = this.host.HeadHeight * 0.5;
        const off = 5;
        ctx.moveTo(this.vec.x, this.vec.y + theHeight - off);
        ctx.lineTo(this.vec.x + 10, this.vec.y + theHeight - off);

        ctx.moveTo(this.vec.x, this.vec.y + theHeight);
        ctx.lineTo(this.vec.x + 10, this.vec.y + theHeight);

        ctx.moveTo(this.vec.x, this.vec.y + theHeight + off);
        ctx.lineTo(this.vec.x + 10, this.vec.y + theHeight + off);

        // }
        ctx.closePath();
        ctx.stroke();

        ctx.restore();
    }

}