import { NVector3, NumericalMotion } from '../../../../../';
import { SubMenuIconCommon } from './SubMenuIconCommon';
import { MenuCommon } from './MenuCommon';
import { MenuPreset } from '../../Core/MenuPreset';
// import { MenuRenderer } from '../../Core/MenuRenderer';

export class SubMenuCommonFly extends SubMenuIconCommon {
    vec: NVector3;
    boundarVecs: NVector3[];

    vecSub: NVector3 = NVector3.Origin();
    widthSub: number;
    heightSub: number;

    title: string;
    height: number;
    width: number;
    isHide: boolean;
    isHover: boolean;
    offY: number;

    isClickable: boolean = true;

    constructor(menu: MenuCommon, title: string, yOff: number) {
        super(menu, title, yOff);
        this.menu = menu;
        this.title = title;
        this.vec = new NVector3(this.menu.vec.x, this.menu.vec.y + yOff);
        this.height = this.menu.pullDownMenuWrapper.theme.heightEach;
        this.width = this.menu.pullDownMenuWrapper.theme.widthEach;
        this.offY = this.height * 0.5;
        this.Init();
    }
    Init() {
        this.widthSub = this.menu.pullDownMenuWrapper.theme.widthEach;
        this.vecSub = new NVector3(this.vec.x + this.menu.pullDownMenuWrapper.theme.widthEach + 1, this.vec.y + 1);
    }
    MouseDown() {
        for (let i = 0; i < this.menus.length; ++i) {
            if (this.menus[i].isHover) {
                this.menus[i].MouseDown();
            }
        }
    }
    MouseInside(x: number, y: number) {
        return NVector3.IsInsideOfVecs(new NVector3(x, y), this.ComputeBoundary());
    }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        if (this.isHide) { return; }
        if (this.MouseInside(this.menu.pullDownMenuWrapper.mouse.x, this.menu.pullDownMenuWrapper.mouse.y)) {
            this.isHover = true;

            if (this.menu.pullDownMenuWrapper.nUI.pullDownMenuWrapper.menuRenderer.captureFlyout && this.menu.pullDownMenuWrapper.nUI.pullDownMenuWrapper.menuRenderer.captureFlyout !== this) {
                this.menu.pullDownMenuWrapper.nUI.pullDownMenuWrapper.menuRenderer.captureFlyout.isHover = false;
            }
            this.menu.pullDownMenuWrapper.nUI.pullDownMenuWrapper.menuRenderer.captureFlyout = this;
        } else {
            if (this.menu.pullDownMenuWrapper.nUI.pullDownMenuWrapper.menuRenderer.captureFlyout === undefined) {
                this.isHover = false;
            }
        }

        if (this.isHover) {
            this.t += this.menu.pullDownMenuWrapper.theme.animationSpeed;
            if (this.t > 1) { this.t = 1.0; }
            MenuPreset.RoundedRectFill(ctx, this.menu.pullDownMenuWrapper.theme.colSelectedBG, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width * NumericalMotion.EaseOutQuad(this.t), this.height), 0);

            if (this.isClickable) {
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colTextHover;
            } else {
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colTextDisable;
            }
        } else {
            this.t = 0;
            if (this.isClickable) {
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colText;
            } else {
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colTextDisable;
            }
        }
        ctx.shadowBlur = 0;
        ctx.shadowOffsetY = 0;
        ctx.shadowOffsetX = 0;
        ctx.font = this.menu.pullDownMenuWrapper.theme.font;
        // ctx.textAlign = this.menu.pullDownMenuWrapper.theme.textAlign;
        ctx.fillText(this.title, this.vec.x + this.menu.pullDownMenuWrapper.textOffX, this.vec.y + this.menu.pullDownMenuWrapper.textOffY);

        if (this.isHover) {
            this.heightSub = this.menu.pullDownMenuWrapper.theme.heightEach * this.menus.length;
            MenuPreset.RoundedRectFillWithCol(ctx, NVector3.GetBoundaryByVecWidthHeight(this.vecSub, this.widthSub, this.heightSub), 3, this.menu.pullDownMenuWrapper.theme.colBG);

            for (let i = 0; i < this.menus.length; ++i) {
                this.menus[i].RenderPost(ctx);
            }
        }
    }
}