import { NVector3 } from '../../';
export class CanvasDrawing {
    public static LineByVecs(ctx: CanvasRenderingContext2D, v0: NVector3, v1: NVector3, width: number = 1, fill: string = '#ff0000') {
        ctx.strokeStyle = fill;
        ctx.lineWidth = width;
        ctx.beginPath();
        ctx.moveTo(v0.x, v0.y);
        ctx.lineTo(v1.x, v1.y);
        ctx.closePath();
        ctx.stroke();
    }
    public static Line(ctx: CanvasRenderingContext2D, x0: number, y0: number, x1: number, y1: number, width: number = 1, fill: string = '#ff0000') {
        ctx.strokeStyle = fill;
        ctx.lineWidth = width;
        ctx.beginPath();
        ctx.moveTo(x0, y0);
        ctx.lineTo(x1, y1);
        ctx.closePath();
        ctx.stroke();
    }
    public static Point(ctx: CanvasRenderingContext2D, x: number, y: number, r: number = 5, fill: string = '#ff0000') {
        ctx.fillStyle = fill;
        ctx.beginPath();
        ctx.arc(x, y, r, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
    }
    public static PointByNVec(ctx: CanvasRenderingContext2D, v: NVector3, r: number = 5, fill: string = '#ff0000') {
        ctx.fillStyle = fill;
        ctx.beginPath();
        ctx.arc(v.x, v.y, r, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
    }
    public static PointByNVecAni(ctx: CanvasRenderingContext2D, v: NVector3, r: number = 5, t: number, fill: string = '#ff0000') {
        ctx.fillStyle = fill;
        ctx.beginPath();
        ctx.arc(v.x, v.y, r + (1 + Math.sin(t)) * 2, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
    }

    // .....................................................
    public static GridLineOrigin(ctx: CanvasRenderingContext2D, xNum: number = 10, yNum: number = 10, xOff: number = 20, yOff: number = 20) {
        const xStart = - ((xNum * xOff) * 0.5);
        const yStart = - ((yNum * yOff) * 0.5);

        ctx.lineWidth = 0.1;

        for (let y = 0; y <= yNum; ++y) {
            // for (let x = 0; x <= xNum; ++x) {
            //     ctx.beginPath();
            //     ctx.arc(xStart + (xOff * x), yStart + (yOff * y), 1, 0, Math.PI * 2);
            //     ctx.closePath();
            //     ctx.fill();
            // }
            ctx.beginPath();
            ctx.moveTo(xStart, yStart + (yOff * y));
            ctx.lineTo((xNum * xOff) * 0.5, yStart + (yOff * y));
            ctx.closePath();
            ctx.stroke();

            ctx.beginPath();
            ctx.moveTo(xStart + (xOff * y), yStart);
            ctx.lineTo(yStart + (yOff * y), (yNum * yOff) * 0.5);
            ctx.closePath();
            ctx.stroke();
        }

    }
    public static GridPointOrigin(ctx: CanvasRenderingContext2D, xNum: number = 10, yNum: number = 10, xOff: number = 20, yOff: number = 20) {
        const xStart = - ((xNum * xOff) * 0.5);
        const yStart = - ((yNum * yOff) * 0.5);
        // console.log(xStart, yStart);
        ctx.lineWidth = 0.1;

        for (let y = 0; y <= yNum; ++y) {
            for (let x = 0; x <= xNum; ++x) {
                ctx.beginPath();
                ctx.arc(xStart + (xOff * x), yStart + (yOff * y), 1, 0, Math.PI * 2);
                ctx.closePath();
                ctx.fill();
            }

        }

    }

    constructor() {
        //
    }
}