import { NUI } from '../NUI';

export class UICanvas {
    nUI: NUI;
    uiCanvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;
    isActive: boolean = true;
    constructor(nUI: NUI) {
        this.nUI = nUI;
        this.InitCanvas();
    }
    public ResizeCanvas(width: number, height: number) {
        this.ctx.canvas.width = width;
        this.ctx.canvas.height = height;
    }
    private InitCanvas() {
        this.uiCanvas = document.createElement('canvas');
        this.uiCanvas.id = 'njscore-web-ui';
        this.uiCanvas.className = 'njscore-web-ui';
        this.uiCanvas.style.position = 'absolute';
        this.uiCanvas.style.pointerEvents = 'none';
        this.uiCanvas.style.zIndex = '10';
        this.uiCanvas.width = this.nUI.divHost.clientWidth;
        this.uiCanvas.height = this.nUI.divHost.clientHeight;

        this.nUI.divHost.appendChild(this.uiCanvas);
        const ctx = this.uiCanvas.getContext('2d');
        if (ctx) { this.ctx = ctx; }
        this.Loop();
    }
    private Loop() {
        if (!this.isActive) { this.ctx.clearRect(0, 0, this.uiCanvas.width, this.uiCanvas.height); return; }
        // console.log(this.uiCanvas.width);
        requestAnimationFrame(() => (this.Loop()));
        this.ctx.clearRect(0, 0, this.uiCanvas.width, this.uiCanvas.height);

        this.Render(this.ctx);
    }
    Render(ctx: CanvasRenderingContext2D) {
        //
    }
}