import { NVector3 } from '../../../Core/CoreGeometry/NGeometry/NVector3';
import { MouseEventData } from '../../../IO/EventInteraction/MouseInteraction';
import { NUI } from '../NUI';
import { IndePanelRender } from './Core/IndePanelRender';
import { UIWrapperBase } from '../Core/Base/UIWrapperBase';
import { ControllerTheme } from '../Core/ControllerTheme';
import { ActiveCommand } from './Core/ActiveCommand';
export class IndependentPanelWrapper extends UIWrapperBase {

    menuOff: number = 10;
    textOffX: number = 12;
    textOffY: number = 10;

    nUI: NUI;
    theme: ControllerTheme;

    m: NVector3;
    indePanelRender: IndePanelRender;

    isActivated: boolean = false;
    activeCommand: ActiveCommand;

    constructor(nUI: NUI, x: number = 0, y: number = 0, width: number = 0, height: number = 0) {
        super();
        this.nUI = nUI;
        this.theme = this.nUI.theme;
        this.width = width;
        this.height = height;
        this.vec = new NVector3(x, y);

        // console.log(this.width, this.height);

        this.textOffY = this.theme.textYOff;

        this.m = new NVector3(0, 0);
        this.indePanelRender = new IndePanelRender(this);

        this.Init();
    }
    Init() {
        this.activeCommand = new ActiveCommand(this);
    }
    Resize(width: number, height: number) {
        //
    }
    RenderPost(ctx: CanvasRenderingContext2D) {
        /* */
    }
    MouseMove(e: MouseEventData) {
        // console.log(e);
        this.m.x = e.x; this.m.y = e.y;
        this.indePanelRender.MouseMove(e);
    }
    MouseDrag(e: MouseEventData) {
        this.indePanelRender.MouseDrag(e);
    }
    MouseDown(e: MouseEventData) {
        this.indePanelRender.MouseDown(e);

    }
    Reset() {
        this.indePanelRender.Reset();
    }
    ShowMenu(v: boolean) {
        this.indePanelRender.ShowMenu(v);
    }
    Destroy() {
        this.indePanelRender.RemoveAll();
    }
    set stopPropagation(v: boolean) {
        this.nUI.stopPropagation = v;  // this.nUI.viewController.viewAppCoreWrapper.viewIOWrapper.eventInteraction.stopPropagation = v;
    }
    set enablePan(v: boolean) {
        this.nUI.enablePan = v;
        // this.viewUIWrapper.viewController.viewAppCoreWrapper.viewIOWrapper.  = v;
    }
}