import { NVector3 } from '../../../../../../';
import { PullDownMenuWrapper } from '../../../PullDownMenuWrapper';
export abstract class MenuBase {
    pullDownMenuWrapper: PullDownMenuWrapper;
    title: string;
    width: number;
    height: number;
    vec: NVector3;
    vecSub: NVector3;
    boundarVecs: NVector3[];
    boundarVecsSub: NVector3[];
    isHover: boolean = false;
    isHide: boolean = false;
    isSelected: boolean = false;
    isCaptured: boolean = false;
    constructor(pullDownMenuWrapper: PullDownMenuWrapper) {
        this.pullDownMenuWrapper = pullDownMenuWrapper;
        this.vec = NVector3.Origin();
        this.vecSub = NVector3.Origin();
        this.pullDownMenuWrapper.menuRenderer.Add(this);
    }
    get IsHover() {
        return this.isHover;
    }
    set IsHover(v: boolean) {
        this.isHover = v;
    }
    public ComputeBoundary() {
        this.boundarVecs = [];
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y + this.height, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y + this.height, this.vec.z));
        return this.boundarVecs;
    }
    public IsMouseInside(x: number, y: number) {
        return NVector3.IsInsideOfVecs(new NVector3(x, y), this.ComputeBoundary());
    }
    public MouseDown() {
        /* */
    }
    public Render(ctx: CanvasRenderingContext2D) {
        this.ComputeBoundary();
        this.RenderPost(ctx);
    }
    public Remove() {
        this.pullDownMenuWrapper.menuRenderer.Remove(this);
    }
    public abstract RenderPost(ctx: CanvasRenderingContext2D): void;
}