export enum ANODE_TYPE {
    DEFAULT = 'DEFAULT',
    START = 'START',
    END = 'END',
}

export class ANode {
    vec: NVector3;
    edges: AEdge[];
    nNodes: ANode[];
    id: number;

    raidus: number = 35;

    t: number = 0.0;
    temp: number = -1;

    type: ANODE_TYPE;
    isVisited: boolean;

    otherValue: number = 0;

    constructor(id: number, v: NVector3) {
        this.id = id;
        this.vec = v;
        this.edges = [];
        this.nNodes = [];
        this.isVisited = false;
        this.type = ANODE_TYPE.DEFAULT;
    }
    isEqual(v: NVector3) {
        if (this.vec === v) { return true; }
        if (NVector3.Distance(this.vec, v) < 0.5) { return true; }
        return false;
    }
    Render(ctx: CanvasRenderingContext2D) {
        // console.log('dd');
        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, 8, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();

        ctx.fillStyle = '#ffffff';
        ctx.textAlign = 'center';
        ctx.fillText('n' + this.id.toString(), this.vec.x, this.vec.y + 3.5);
        // this.t += 0.05;
    }
    RnnderNodeArea(ctx: CanvasRenderingContext2D) {

        let ra = this.raidus + Math.cos(this.t) * 5;
        if (this.type === ANODE_TYPE.START || this.type === ANODE_TYPE.END) {
            ctx.fillStyle = 'rgba(0,255,0,0.3)';
        } else {
            ctx.fillStyle = 'rgba(0,0,0,0.3)';
        }
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, ra, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
    }
}
export class AEdge {
    n0: ANode;
    n1: ANode;
    vec: NVector3;
    id: number;
    constructor(id: number, n0: ANode, n1: ANode) {
        this.id = id;
        this.n0 = n0; n0.edges.push(this);
        this.n1 = n1; n1.edges.push(this);
    }
    Render(ctx: CanvasRenderingContext2D) {
        this.ComputeCenter();
        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, 14, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();

        ctx.fillStyle = '#ffffff';
        ctx.textAlign = 'center';
        ctx.fillText('E ' + this.id.toString(), this.vec.x, this.vec.y + 3.5);
    }
    RenderDim(ctx: CanvasRenderingContext2D) {
        // this.ComputeCenter();
        // ctx.fillStyle = '#000000';
        // ctx.beginPath();
        // ctx.arc(this.vec.x, this.vec.y, 14, 0, Math.PI * 2);
        // ctx.closePath();
        // ctx.fill();

        // ctx.fillStyle = '#ffffff';
        // ctx.textAlign = 'center';
        // ctx.fillText('E ' + this.id.toString(), this.vec.x, this.vec.y + 3.5);
        if (this.n0.vec.x < this.n1.vec.x) {
            // ADrawingPreset.LineDim(ctx, 0, this.n0.vec.x, this.n0.vec.y - 4, this.n1.vec.x, this.n1.vec.y, CLine.DistancefromGeoV(this.n0.vec, this.n1.vec));
        } else {
            // ADrawingPreset.LineDim(ctx, 0, this.n1.vec.x, this.n1.vec.y - 4, this.n0.vec.x, this.n0.vec.y, CLine.DistancefromGeoV(this.n0.vec, this.n1.vec));
        }
    }
    private ComputeCenter() {
        this.vec = NVector3.MidVec(this.n0.vec, this.n1.vec);
    }
}

export class ALineGraph {

    nodes: ANode[];
    edges: AEdge[];

    p0: NVector3;
    p1: NVector3;

    startNode: ANode;
    endNode: ANode;

    path: ANode[];

    constructor() {
        this.edges = [];
        this.nodes = [];
    }
    // tslint:disable-next-line:no-any
    BuildByALines(lns: any) { // ALine[]) {
        if (lns.length === 0) { return; }
        let lines = lns;
        let idNode = 0;
        // console.log('==================================');
        // console.log('input length of line:', lns.length);
        let nodes: ANode[] = [];
        nodes.push(new ANode(idNode++, lines[0].p0.vec));
        nodes.push(new ANode(idNode++, lines[0].p1.vec));
        for (let i = 1; i < lines.length; ++i) {
            let isContainN0 = true, isContainN1 = true;
            for (let j = 0; j < nodes.length; ++j) {
                if (nodes[j].isEqual(lines[i].p0.vec)) { isContainN0 = false; }
                if (nodes[j].isEqual(lines[i].p1.vec)) { isContainN1 = false; }
            }
            if (isContainN0) { nodes.push(new ANode(idNode++, lines[i].p0.vec)); }
            if (isContainN1) { nodes.push(new ANode(idNode++, lines[i].p1.vec)); }

        }
        this.nodes = nodes;

        this.edges = [];
        for (let i = 0; i < lines.length; ++i) {
            let n0: ANode | undefined;
            let n1: ANode | undefined;
            for (let j = 0; j < nodes.length; ++j) {
                if (nodes[j].isEqual(lines[i].p0.vec)) { n0 = nodes[j]; }
                if (nodes[j].isEqual(lines[i].p1.vec)) { n1 = nodes[j]; }
            }
            if (n0 && n1) {
                this.edges.push(new AEdge(i, n0, n1));
            }
        }
        this.ComputeNeighborNode();
    }
    ComputeNeighborNode() {
        for (const n of this.nodes) {
            for (const e of n.edges) {
                if (n !== e.n0) { n.nNodes.push(e.n0); }
                if (n !== e.n1) { n.nNodes.push(e.n1); }
            }
        }
    }
    ComputePathFinding(v0: NVector3, v1: NVector3) {
        this.p0 = v0;
        this.p1 = v1;
        this.startNode = this.FindClosestNodeByVec(v0);
        this.startNode.type = ANODE_TYPE.START;
        this.endNode = this.FindClosestNodeByVec(v1);
        this.endNode.type = ANODE_TYPE.END;
        console.log(this.startNode, this.endNode);
    }
    UpdatePathFinding() {
        this.ResetPath();
        if (this.p0 && this.p1) {
            this.ComputePathFinding(this.p0, this.p1);
            this.path = this.ComputeShortestPath();
        }
    }
    FindClosestNodeByVec(v: NVector3) {
        // let i = this.nodes.length - 1;
        let minDis = Number.MAX_SAFE_INTEGER;
        let si = -1;
        for (let i = 0; i < this.nodes.length; ++i) {
            let dis = this.nodes[i].vec.Distance(v);
            if (dis < minDis) { minDis = dis; si = i; }
        }
        return this.nodes[si];
    }
    Cost(current: ANode, other: ANode) {
        // let v = current.vec;
        let dis = NVector3.Distance(current.vec, other.vec);

        let isInside = false;
        // for (let i = 0; i < ACircle.list.length; ++i) {
        //     let cirDis = NVector3.Distance(ACircle.list[i].GetCenterVec(), v);
        //     // console.log(cirDis, ACircle.list[i].r0);
        //     if (cirDis < ACircle.list[i].r0) {
        //         isInside = true;
        //     }
        // }
        if (isInside) {
            console.log(current.id);
            current.temp += 1000000;
            return dis;
        } else {
            return dis;
        }
    }
    DFS(index: number) {
        if (this.nodes[index]) { return; }
        this.nodes[index].isVisited = true;
        for (let j = 0; j < this.nodes[index].nNodes.length; ++j) {
            // do
            const ni = this.nodes[index][j];
            this.DFS(ni);
        }
    }
    BFS(index: number) {
        let front: ANode[] = [];
        front.push(this.nodes[index]);
        this.nodes[index].isVisited = true;
        for (let i = 0; i < 20000; ++i) {
            if (front.length === 0) { break; }
            const current = front[0];
            front.shift();
            let safeguard = 0;
            for (let np of current.nNodes) {

                if (!np.isVisited) {

                    // do
                    front.push(np);

                    np.isVisited = true;
                }

                // const newcost = this.Cost(current, np);
                // const newtemp = current.temp + newcost; // + current.otherValue;
                // if (np.temp === -1.0 || newtemp < np.temp) { // when we need this condition -> np.temp > newtemp ?
                //     np.temp = newtemp;
                //     front.push(np);

                // }

                if (++safeguard > 100) { break; }
            }
        }

    }
    ComputeShortestPath() { // , Graph: ALineGraph, transitionCostDelegate: cost): ANode[] { //, PixelMap pMap, transitionCostDelegate cost) {

        let nFirst: ANode = this.startNode; // this.FindClosestNodeByVec(this.p0);
        let nEnd: ANode = this.endNode; // this.FindClosestNodeByVec(this.p1);

        // declaration of path list
        let path: ANode[] = [];

        // reset the temp value
        for (let n of this.nodes) { n.temp = -1.0; }

        // declaration of front list
        let front: ANode[] = [];
        front.push(nFirst);
        nFirst.temp = 0.0;

        // forward propagate
        for (let i = 0; i < 20000; ++i) {
            if (front.length === 0) { break; }
            const current = front[0];
            front.shift();

            let safeguard = 0;
            for (let np of current.nNodes) {
                const newcost = this.Cost(current, np);
                const newtemp = current.temp + newcost; // + current.otherValue;
                if (np.temp === -1.0 || newtemp < np.temp) { // when we need this condition -> np.temp > newtemp ?
                    np.temp = newtemp;
                    front.push(np);

                }
                if (++safeguard > 100) { break; }
            }
        }

        // // backward propagate
        if (nEnd.temp !== -1.0) {
            path.push(nEnd);
            let current = nEnd;
            for (let i = 0; i < 20000; ++i) {
                let smallest = current;
                for (let np of current.nNodes) {
                    if (np.temp < smallest.temp) { smallest = np; }
                }
                if (smallest === current) { break; } // which means that?
                current = smallest;
                path.push(current);
                if (current === nFirst) { break; }
            }
        }
        console.log(path);
        this.path = path;
        return path;
    }

    Print() {
        console.log('number of edge', this.edges.length);
        console.log('number of node:', this.nodes.length);
        for (let n of this.nodes) {
            let stringEdge = '';
            for (let i = 0; i < n.edges.length; ++i) {
                stringEdge += 'E_' + n.edges[i].id + ' ';
            }

            console.log('N_' + n.id, ' = [', stringEdge, ']');
        }
    }
    Render(ctx: CanvasRenderingContext2D) {
        // console.log('graphic');
        this.RnnderNodeArea(ctx);
        this.RenderNode(ctx);
        this.RenderEdge(ctx);
        this.RenderDim(ctx);

        this.RenderPath(ctx);
    }
    RnnderNodeArea(ctx: CanvasRenderingContext2D) {
        for (let n of this.nodes) {
            n.RnnderNodeArea(ctx);
        }
    }
    RenderNode(ctx: CanvasRenderingContext2D) {
        for (let n of this.nodes) {
            n.Render(ctx);
        }
    }
    RenderEdge(ctx: CanvasRenderingContext2D) {
        for (let e of this.edges) {
            e.Render(ctx);
        }
    }
    RenderDim(ctx: CanvasRenderingContext2D) {
        for (let e of this.edges) {
            e.RenderDim(ctx);
        }
    }
    RenderPath(ctx: CanvasRenderingContext2D) {
        if (this.path && this.path.length > 0) {
            ctx.lineWidth = 10;
            ctx.beginPath();
            ctx.strokeStyle = 'rgba(255,0,0,0.5)';
            ctx.moveTo(this.path[0].vec.x, this.path[0].vec.y);
            for (let i = 1; i < this.path.length; ++i) {
                ctx.lineTo(this.path[i].vec.x, this.path[i].vec.y);
            }
            ctx.stroke();
        }
    }
    private ResetPath() {
        for (let i = 0; i < this.nodes.length; ++i) { this.nodes[i].type = ANODE_TYPE.DEFAULT; }
    }
}