import { NVector3 } from '../../../../../..';

// import { NJSWebCore } from '../../../../../../NJSWebCore';

import { PullDownMenuWrapper } from '../../../PullDownMenuWrapper';

import { MenuCommon } from '../../Common/MenuCommon';
import { SubMenuCommonSection } from '../../Common/SubMenuCommonSection';
import { SubMenuIconCommon } from '../../Common/SubMenuIconCommon';
import { SubMenuIconFlyCommon } from '../../Common/SubMenuIconFlyCommon';
import { SubMenuCommonFly } from '../../Common/SubMenuCommonFly';
import { SubMenuIconFlyCommonSection } from '../../Common/SubMenuIconFlyCommonSection';

export class MenuNTFCustom extends MenuCommon {

    pullDownMenuWrapper: PullDownMenuWrapper;

    title: string;
    numInterval: number;
    numSubInterval: number;
    // .........................................
    projectMLProject: SubMenuCommonFly;
    projectMLProjectNumSubInterval: number = 0;

    // .........................................
    projectTFProject: SubMenuCommonFly;
    projectTFProjectNumSubInterval: number = 0;
    // .........................................
    // tslint:disable-next-line:no-any
    constructor(pullDownMenuWrapper: PullDownMenuWrapper, ac: any, title: string, xOff: number) {
        super(pullDownMenuWrapper, ac);
        this.pullDownMenuWrapper = pullDownMenuWrapper;
        this.vec = new NVector3(this.pullDownMenuWrapper.vec.x + xOff, this.pullDownMenuWrapper.vec.y);
        this.height = this.pullDownMenuWrapper.height;
        this.title = title;
        this.width = (this.pullDownMenuWrapper.textOffX * 2) + (this.title.length * 6);
        this.Init();
    }
    public Init() {
        this.AppendCommand();
        this.heightSub = this.pullDownMenuWrapper.theme.heightEach * this.menus.length;
        this.widthSub = this.pullDownMenuWrapper.theme.widthEach;
        this.vecSub = new NVector3(this.vec.x, this.vec.y + this.pullDownMenuWrapper.bar.height + 1);
    }
    public AddApp(v: string) {
        const action = v.split('@');
        let subM;
        switch (action[0]) {
            case 'mlProject':
                if (action[1] === '--') {
                    this.projectMLProject.menus.push(new SubMenuIconFlyCommonSection(this.projectMLProject, 'section', this.projectMLProject.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.projectMLProjectNumSubInterval++));
                } else {
                    subM = new SubMenuIconFlyCommon(this.projectMLProject, action[1], this.projectMLProject.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.projectMLProjectNumSubInterval++);
                    subM.isClickable = true;
                    subM.MouseDownPost = () => {
                        this.ac.LoadDefinition(action[1]);
                    };
                    this.projectMLProject.menus.push(subM);
                }
                break;
            case 'tfProject':
                if (action[1] === '--') {
                    this.projectTFProject.menus.push(new SubMenuIconFlyCommonSection(this.projectTFProject, 'section', this.projectTFProject.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.projectTFProjectNumSubInterval++));
                } else {
                    subM = new SubMenuIconFlyCommon(this.projectTFProject, action[1], this.projectTFProject.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.projectTFProjectNumSubInterval++);
                    subM.isClickable = true;
                    subM.MouseDownPost = () => {
                        this.ac.LoadDefinition(action[1]);
                    };
                    this.projectTFProject.menus.push(subM);
                }
                break;
            default:
                break;
        }
    }
    public AppendCommand() {
        // const ac = this.pullDownMenuWrapper.viewUIWrapper.viewController.model.controllerCoreWrapper.activeCommandController;
        this.numInterval = 0; this.numSubInterval = 0;
        let m = new SubMenuIconCommon(this, 'dummy', this.pullDownMenuWrapper.bar.height);
        // .............................................................................................................................. sub flyout menu
        m = new SubMenuIconCommon(this, 'About TF App', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++);
        // m.isClickable = false;
        m.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.NJSWebCore.viewController) { this.pullDownMenuWrapper.viewController.NJSWebCore.viewController.indePanel.activeCommand.AboutDrawingTool(); }
            // this.pullDownMenuWrapper.viewController.NJSWebCore.ACController.Stoppropagation = true; this.pullDownMenuWrapper.Reset();
            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++));

        // ............................................................ Project ML
        m = new SubMenuCommonFly(this, 'Project ML Project', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++);
        m.isClickable = true;
        this.projectMLProject = m as SubMenuCommonFly;
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++));

        // .............................................................................................................................. sub flyout menu

        m = new SubMenuCommonFly(this, 'Project TF', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++);
        m.isClickable = true;
        this.projectTFProject = m as SubMenuCommonFly;
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++));

        // .............................................................................................................................. sub flyout menu
        m = new SubMenuCommonFly(this, 'Helpers', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++);
        m.isClickable = false;
        let mFly = m as SubMenuCommonFly; this.numSubInterval = 0;
        let subM = new SubMenuIconFlyCommon(mFly, 'Log', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool) { this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool.activeCommandController.ShiftLog(); }
            // ac.ShiftLog();
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'Unit', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool) { this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool.activeCommandController.ShiftUnit(); }
            // ac.ShiftUnit();
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'View cropping', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool) { this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool.activeCommandController.SetActiveCommand(ACTIVECOMMAND_MODE.CRECTANGLE_CROP); }
            // ac.SetActiveCommand(ACTIVECOMMAND_MODE.CRECTANGLE_CROP);
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++));

        m = new SubMenuIconCommon(this, 'Save and capture screen as an image', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool) { this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool.SaveImg(); }
            // ac.save();
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++));

        m = new SubMenuCommonFly(this, 'Playground', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++);
        m.isClickable = true;
        mFly = m as SubMenuCommonFly; this.numSubInterval = 0;
        subM = new SubMenuIconFlyCommon(mFly, 'TF PG 01', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.isClickable = true;
        subM.MouseDownPost = () => {
            this.ac.LoadDefinition('TFPlayground01');
        };
        m.menus.push(subM);
        subM = new SubMenuIconFlyCommon(mFly, 'LinearRegression', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.isClickable = true;
        subM.MouseDownPost = () => {
            this.ac.LoadDefinition('project01');
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'LinearRegression, GD', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.isClickable = true;
        subM.MouseDownPost = () => {
            this.ac.LoadDefinition('project02');
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'LinearRegression, GD', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.isClickable = true;
        subM.MouseDownPost = () => {
            this.ac.LoadDefinition('project03');
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'LinearRegression, GD', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * this.numSubInterval++);
        subM.isClickable = true;
        subM.MouseDownPost = () => {
            this.ac.LoadDefinition('project04');
        };
        m.menus.push(subM);
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++));

        m = new SubMenuIconCommon(this, 'Quit', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * this.numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool) { this.pullDownMenuWrapper.viewController.NJSWebCore.appDrawingTool.Destroy(); }
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);

    }

}