import { NVector3 } from '../../../../..';
import { PullDownMenuWrapper } from '../../PullDownMenuWrapper';
import { MenuCommon } from '../Common/MenuCommon';
import { SubMenuCommonSection } from '../Common/SubMenuCommonSection';
import { SubMenuIconCommon } from '../Common/SubMenuIconCommon';
import { SubMenuIconFlyCommon } from '../Common/SubMenuIconFlyCommon';
import { SubMenuCommonFly } from '../Common/SubMenuCommonFly';
export class MenuNJSLabCoreSandbox extends MenuCommon {
    pullDownMenuWrapper: PullDownMenuWrapper;
    title: string;
    // tslint:disable-next-line:no-any
    constructor(pullDownMenuWrapper: PullDownMenuWrapper, ac: any, title: string, xOff: number) {
        super(pullDownMenuWrapper, ac);
        this.pullDownMenuWrapper = pullDownMenuWrapper;
        this.vec = new NVector3(this.pullDownMenuWrapper.vec.x + xOff, this.pullDownMenuWrapper.vec.y);
        this.height = this.pullDownMenuWrapper.height;
        this.title = title;
        this.width = (this.pullDownMenuWrapper.textOffX * 2) + (this.title.length * 6);
        this.Init();
    }
    Init() {
        this.AppendCommand();
        this.heightSub = this.pullDownMenuWrapper.theme.heightEach * this.menus.length;
        this.widthSub = this.pullDownMenuWrapper.theme.widthEach;
        this.vecSub = new NVector3(this.vec.x, this.vec.y + this.pullDownMenuWrapper.bar.height + 1);
    }
    AppendCommand() {
        // const ac = this.pullDownMenuWrapper.viewUIWrapper.viewController.model.controllerCoreWrapper.activeCommandController;
        let numInterval = 0, numSubInterval = 0;
        let m = new SubMenuIconCommon(this, 'dummy', this.pullDownMenuWrapper.bar.height);
        // .............................................................................................................................. sub flyout menu
        m = new SubMenuIconCommon(this, 'About NCanvas App', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        // m.isClickable = false;
        m.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.viewController) { this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.viewController.indePanel.activeCommand.AboutDrawingTool(); }
            // this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.ACController.Stoppropagation = true; this.pullDownMenuWrapper.Reset();
            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        m = new SubMenuCommonFly(this, 'Files', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        let mFly = m as SubMenuCommonFly; numSubInterval = 0;
        let subM = new SubMenuIconFlyCommon(mFly, 'Open', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {
            // this.pullDownMenuWrapper.viewUIWrapper.viewController.model.appCoreWrapper.appAccessibility.mode = INTERACTION_MODE.DRAWING;

            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);
        subM = new SubMenuIconFlyCommon(mFly, 'Save', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {

            // this.pullDownMenuWrapper.viewUIWrapper.viewController.model.appCoreWrapper.appAccessibility.mode = INTERACTION_MODE.INTERACTIVE_VIEWER;

            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);
        subM = new SubMenuIconFlyCommon(mFly, 'Download', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {

            // this.pullDownMenuWrapper.viewUIWrapper.viewController.model.appCoreWrapper.appAccessibility.mode = INTERACTION_MODE.OBSERVE_VIEWER;

            // this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        // .............................................................................................................................. sub flyout menu
        m = new SubMenuCommonFly(this, 'Helpers', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        mFly = m as SubMenuCommonFly; numSubInterval = 0;
        subM = new SubMenuIconFlyCommon(mFly, 'Log', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool) { this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool.activeCommandController.ShiftLog(); }
            // ac.ShiftLog();
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'Unit', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool) { this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool.activeCommandController.ShiftUnit(); }
            // ac.ShiftUnit();
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'View cropping', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool) { this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool.activeCommandController.SetActiveCommand(ACTIVECOMMAND_MODE.CRECTANGLE_CROP); }
            // ac.SetActiveCommand(ACTIVECOMMAND_MODE.CRECTANGLE_CROP);
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        m = new SubMenuIconCommon(this, 'Save and capture screen as an image', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool) { this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool.SaveImg(); }
            // ac.save();
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);

        m = new SubMenuIconCommon(this, 'Quit', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool) { this.pullDownMenuWrapper.viewController.njslabWebCoreSandbox.appDrawingTool.Destroy(); }
            //  this.pullDownMenuWrapper.stoppropagation = false; this.pullDownMenuWrapper.Reset();
        };
        this.menus.push(m);
    }

}