import { NVector3, NumericalMotion } from '../../../../../';
import { MenuCommon } from './MenuCommon';
import { MenuPreset } from '../../Core/MenuPreset';
export class SubMenuIconCommon {
    vec: NVector3;
    boundarVecs: NVector3[];
    menu: MenuCommon;
    menus: SubMenuIconCommon[] = [];
    title: string;
    height: number;
    width: number;
    isHide: boolean;
    isHover: boolean;
    isClickable: boolean = true;
    t: number = 0;
    constructor(menu: MenuCommon, title: string, yOff: number) {
        this.menu = menu;
        this.title = title;
        this.vec = new NVector3(this.menu.vec.x, this.menu.vec.y + yOff);
        this.height = this.menu.pullDownMenuWrapper.theme.heightEach;
        this.width = this.menu.pullDownMenuWrapper.theme.widthEach;
    }
    ComputeBoundary() {
        this.boundarVecs = [];
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y + this.height, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y + this.height, this.vec.z));
        return this.boundarVecs;
    }
    MouseInside(x: number, y: number) {
        return NVector3.IsInsideOfVecs(new NVector3(x, y), this.ComputeBoundary());
    }
    MouseDown() {
        if (!this.isClickable) { return; }
        this.MouseDownPost();
        // this.menu.pullDownMenuWrapper.stopPropagation22 = true;
    }
    MouseDownPost() {
        /* */
    }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        if (this.isHide) { return; }
        if (this.MouseInside(this.menu.pullDownMenuWrapper.mouse.x, this.menu.pullDownMenuWrapper.mouse.y)) {
            this.isHover = true;
            this.t += this.menu.pullDownMenuWrapper.theme.animationSpeed;
            this.menu.pullDownMenuWrapper.menuRenderer.captureFlyout = undefined;
        } else {
            this.t = 0;
            this.isHover = false;
        }

        if (this.isClickable) {
            if (this.isHover) {
                if (this.t > 1) { this.t = 1.0; }
                MenuPreset.RoundedRectFill(ctx, this.menu.pullDownMenuWrapper.theme.colSelectedBG, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width * NumericalMotion.EaseOutQuad(this.t), this.height), 0);
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colTextHover;
            } else {
                ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colText;
            }
        } else {
            ctx.fillStyle = this.menu.pullDownMenuWrapper.theme.colTextDisable;
        }

        ctx.shadowBlur = 0;
        ctx.shadowOffsetY = 0;
        ctx.shadowOffsetX = 0;
        ctx.font = this.menu.pullDownMenuWrapper.theme.font;
        // ctx.textAlign = this.menu.pullDownMenuWrapper.theme.textAlign;
        ctx.fillText(this.title, this.vec.x + this.menu.pullDownMenuWrapper.textOffX, this.vec.y + this.menu.pullDownMenuWrapper.textOffY);
    }
}