import { GeometryBase } from './Base/AbstractGeometryBase';
import { NVector3 } from './NVector3';
import { MathUtility } from '../../NUtility/Math/MathUtility';
// import { Point } from './Point';
export class NLine extends GeometryBase {
    p0: NVector3;
    p1: NVector3;
    public static GetLineFromVecs(vs: NVector3[]) {
        let lns: NLine[] = [];
        for (let i = 0; i < vs.length; ++i) {
            if (i === vs.length - 1) {
                lns.push(new NLine(vs[i], vs[0]));
            } else { lns.push(new NLine(vs[i], vs[i + 1])); }
        }
        return lns;
    }
    public static GetUnOverlapWithLines(ln: NLine[], others: NLine[]) {
        let index = [];
        let newLns = [];
        for (let i = 0; i < ln.length; ++i) {
            index.push(false);
            for (let j = 0; j < others.length; ++j) {
                if (NLine.IsOverlap(ln[i], others[j])) {
                    index[i] = true;
                }
            }
        }
        for (let i = 0; i < index.length; ++i) {
            if (!index[i]) { newLns.push(ln[i]); }
        }
        return newLns;
    }
    public static IsOverlap(ln: NLine, other: NLine): boolean {
        if (ln.p0.IsOverlap(other.p0) && ln.p1.IsOverlap(other.p1)) {
            return true;
        } else if (ln.p0.IsOverlap(other.p1) && ln.p1.IsOverlap(other.p0)) {
            return true;
        } else {
            return false;
        }
    }
    public static RemoveOverlapWithLines(lns: NLine[]) {
        let index = [];
        let nLns = [];
        for (let i = 0; i < lns.length; ++i) {
            let over = false;
            index.push(0);
            for (let j = i + 1; j < lns.length; ++j) {
                over = NLine.IsOverlap(lns[i], lns[j]);
                if (over) { index[i]++; }
            }
        }
        for (let i = 0; i < index.length; ++i) {
            if (index[i] === 0) { nLns.push(lns[i]); }
        }
        return nLns;
    }
    constructor(p0: NVector3, p1: NVector3) {
        super();
        this.p0 = p0; this.p1 = p1;
    }
    public Render(ctx: CanvasRenderingContext2D): void {
        ctx.lineWidth = 0.5;
        ctx.strokeStyle = 'red';
        ctx.beginPath();
        ctx.moveTo(this.p0.x, this.p0.y);
        ctx.lineTo(this.p1.x, this.p1.y);
        ctx.closePath();
        ctx.stroke();
    }
    public DistanceToPoint(p: NVector3) {
        // slope
        let m = (this.p1.y - this.p0.y) / (this.p1.x - this.p0.x),
            // y offset
            b = this.p0.y - (m * this.p0.x),
            d = [];
        // distance to the linear equation
        d.push(Math.abs(p.y - (m * p.x) - b) / Math.sqrt(Math.pow(m, 2) + 1));
        // distance to p0
        d.push(Math.sqrt(Math.pow((p.x - this.p0.x), 2) + Math.pow((p.y - this.p0.y), 2)));
        // distance to p1
        d.push(Math.sqrt(Math.pow((p.x - this.p1.x), 2) + Math.pow((p.y - this.p1.y), 2)));
        // return the smallest distance
        return d.sort((aa, bb) => { return (aa - bb); })[0]; // causes an array to be sorted numerically and ascending;
    }
    public PointCloseToLine(p: NVector3) {
        let dx = this.p1.x - this.p0.x,
            dy = this.p1.y - this.p0.y,
            t = ((p.x - this.p0.x) * dx + (p.y - this.p0.y) * dy) / (dx * dx + dy * dy),
            x = MathUtility.Lerp(this.p0.x, this.p1.x, t),
            y = MathUtility.Lerp(this.p0.y, this.p1.y, t);
        return [x, y];
    }
    public GetLog(): string {
        throw new Error('Method not implemented.');
    }
    public RenderPost(ctx: CanvasRenderingContext2D): void {
        throw new Error('Method not implemented.');
    }
    public IsMouseInside(x: number, y: number): boolean {
        throw new Error('Method not implemented.');
    }
    public Translate(x: number, y: number): void {
        throw new Error('Method not implemented.');
    }
    public Move(x: number, y: number): void {
        throw new Error('Method not implemented.');
    }
    public Remove(): void {
        throw new Error('Method not implemented.');
    }
    Hide(): void {
        throw new Error('Method not implemented.');
    }
}