import { NVector3 } from '../../../../';
// import { ADrawingPreset } from '../../AppDrawing/Canvas/CAnnotation/ADrawingPreset';
// import { ColorUtility } from '../../Core/Utility';

export class IndePanelPreset {

    static theme = {
        'mainMenuNormal': '#ffffff', // '#1267B0', // '#0FAAEA;',
        'mainMenuOver': '#0FAAEA'
    };
    static RoundedRectFill(ctx: CanvasRenderingContext2D, vs: NVector3[], r: number, xOff: number = 0, fillCol: string = '#ffffff', strokeCol: string = '#000000') {
        // let color = s.backgroundColor;
        let radius = r; // parseFloat(s.borderRadius);
        // console.log(color);
        // ctx.save();
        ctx.lineWidth = 1; // parseFloat(s.lineWidth);
        ctx.beginPath();

        ctx.moveTo(vs[0].x + xOff + radius, vs[0].y);
        ctx.lineTo(vs[1].x - xOff - radius, vs[1].y);

        ctx.arcTo(vs[1].x - xOff, vs[1].y, vs[1].x - xOff, vs[1].y + radius, radius);
        ctx.lineTo(vs[2].x - xOff, vs[2].y - radius);

        ctx.arcTo(vs[2].x - xOff, vs[2].y, vs[2].x - xOff - radius, vs[2].y, radius);
        ctx.lineTo(vs[3].x + xOff + radius, vs[3].y);

        ctx.arcTo(vs[3].x + xOff, vs[3].y, vs[3].x + xOff, vs[3].y - radius, radius);
        ctx.lineTo(vs[0].x + xOff, vs[0].y + radius);
        ctx.arcTo(vs[0].x + xOff, vs[0].y, vs[0].x + xOff + radius, vs[0].y, radius);
        ctx.closePath();
        ctx.strokeStyle = strokeCol;
        ctx.fillStyle = fillCol;

        ctx.shadowBlur = 4;
        ctx.shadowColor = 'rgb(0,0,0,0.3)';
        ctx.shadowOffsetY = 2;
        ctx.shadowOffsetX = 2;

        ctx.fill();
        // ctx.stroke();
        // ctx.restore();
    }
    static RoundedRectFillRForEach(ctx: CanvasRenderingContext2D, vs: NVector3[], r0: number, r1: number, r2: number, r3: number, xOff: number = 0, fillCol: string = '#ffffff', strokeCol: string = '#000000', isShadow: boolean = false) {
        // let color = s.backgroundColor;
        let radius0 = r0; // parseFloat(s.borderRadius);
        let radius1 = r1; // parseFloat(s.borderRadius);
        let radius2 = r2; // parseFloat(s.borderRadius);
        let radius3 = r3; // parseFloat(s.borderRadius);
        // console.log(color);
        // ctx.save();
        ctx.lineWidth = 1; // parseFloat(s.lineWidth);
        ctx.beginPath();

        ctx.moveTo(vs[0].x + xOff + radius0, vs[0].y);
        ctx.lineTo(vs[1].x - xOff - radius1, vs[1].y);

        ctx.arcTo(vs[1].x - xOff, vs[1].y, vs[1].x - xOff, vs[1].y + radius1, radius1);
        ctx.lineTo(vs[2].x - xOff, vs[2].y - radius1);

        ctx.arcTo(vs[2].x - xOff, vs[2].y, vs[2].x - xOff - radius2, vs[2].y, radius2);
        ctx.lineTo(vs[3].x + xOff + radius2, vs[3].y);

        ctx.arcTo(vs[3].x + xOff, vs[3].y, vs[3].x + xOff, vs[3].y - radius3, radius3);
        ctx.lineTo(vs[0].x + xOff, vs[0].y + radius3);

        ctx.arcTo(vs[0].x + xOff, vs[0].y, vs[0].x + xOff + radius0, vs[0].y, radius0);
        ctx.closePath();
        ctx.strokeStyle = strokeCol;
        ctx.fillStyle = fillCol;

        if (isShadow) {
            ctx.shadowBlur = 4;
            ctx.shadowColor = 'rgb(0,0,0,0.3)';
            ctx.shadowOffsetY = 2;
            ctx.shadowOffsetX = 2;
        } else {
            ctx.shadowBlur = 0;
            ctx.shadowOffsetY = 0;
            ctx.shadowOffsetX = 0;
        }

        ctx.fill();
        // ctx.stroke();
        // ctx.restore();
    }
    static Text(ctx: CanvasRenderingContext2D, x: number, y: number, text: string, color: string = 'white') {

        ctx.save();
        ctx.fillStyle = color;
        ctx.textBaseline = 'bottom';
        ctx.textAlign = 'left'; // 'start', 'end', 'left', 'center', 'right';
        ctx.fillText(text, x, y - 14);
        ctx.fill();
        ctx.restore();
    }
    static RoundedRectFillWithColorMainMenu(ctx: CanvasRenderingContext2D, title: string, vs: NVector3[], r: number, xOff: number = 0, isOver: boolean, isSel: boolean, col: string, isShadow: boolean = true) {
        let radius = r; // parseFloat(s.borderRadius);
        let color: string = col;

        ctx.save();
        ctx.lineWidth = 2; // parseFloat(s.lineWidth);
        ctx.beginPath();
        ctx.moveTo(vs[0].x + xOff + radius, vs[0].y);
        ctx.lineTo(vs[1].x - xOff - radius, vs[1].y);
        ctx.arcTo(vs[1].x - xOff, vs[1].y, vs[1].x - xOff, vs[1].y + radius, radius);
        ctx.lineTo(vs[2].x - xOff, vs[2].y - radius);
        ctx.arcTo(vs[2].x - xOff, vs[2].y, vs[2].x - xOff - radius, vs[2].y, radius);
        ctx.lineTo(vs[3].x + xOff + radius, vs[3].y);
        ctx.arcTo(vs[3].x + xOff, vs[3].y, vs[3].x + xOff, vs[3].y - radius, radius);
        ctx.lineTo(vs[0].x + xOff, vs[0].y + radius);
        ctx.arcTo(vs[0].x + xOff, vs[0].y, vs[0].x + xOff + radius, vs[0].y, radius);
        ctx.closePath();
        ctx.strokeStyle = color; // s.strokeStyle;
        ctx.fillStyle = color;
        if (isShadow) {
            ctx.shadowBlur = 7;
            ctx.shadowColor = 'rgb(0,0,0,0.5)';
            ctx.shadowOffsetY = 1.5;
            ctx.shadowOffsetX = 1.5;
        } else {
            ctx.shadowBlur = 0;
            ctx.shadowOffsetY = 0;
            ctx.shadowOffsetX = 0;
        }
        ctx.fill();
        ctx.restore();

    }
    static TopRoundedRectFill(ctx: CanvasRenderingContext2D, title: string, vs: NVector3[], r: number, xOff: number = 0, isOver: boolean, isSel: boolean, col: string, isShadow: boolean = true) {
        let radius = r; // parseFloat(s.borderRadius);
        let color: string = col;

        ctx.save();
        ctx.lineWidth = 2; // parseFloat(s.lineWidth);
        ctx.beginPath();
        ctx.moveTo(vs[0].x + xOff + radius, vs[0].y);
        ctx.lineTo(vs[1].x - xOff - radius, vs[1].y);
        ctx.arcTo(vs[1].x - xOff, vs[1].y, vs[1].x - xOff, vs[1].y + radius, radius);
        ctx.lineTo(vs[2].x - xOff, vs[2].y);
        ctx.lineTo(vs[3].x + xOff, vs[3].y);
        ctx.lineTo(vs[0].x + xOff, vs[0].y + radius);
        ctx.arcTo(vs[0].x + xOff, vs[0].y, vs[0].x + xOff + radius, vs[0].y, radius);
        ctx.closePath();
        ctx.strokeStyle = color; // s.strokeStyle;
        ctx.fillStyle = color;
        if (isShadow) {
            ctx.shadowBlur = 4;
            ctx.shadowColor = 'rgb(0,0,0,0.3)';
            ctx.shadowOffsetY = 2;
            ctx.shadowOffsetX = 2;
        } else {
            ctx.shadowBlur = 0;
            ctx.shadowOffsetY = 0;
            ctx.shadowOffsetX = 0;
        }
        ctx.fill();
        ctx.restore();

    }
    constructor() { /* */ }

}