import { NVector3 } from '../../../../..';

import { PullDownMenuWrapper } from '../../PullDownMenuWrapper';

import { MenuCommon } from '../Common/MenuCommon';
import { SubMenuCommonSection } from '../Common/SubMenuCommonSection';
import { SubMenuIconCommon } from '../Common/SubMenuIconCommon';
import { SubMenuIconFlyCommon } from '../Common/SubMenuIconFlyCommon';
import { SubMenuIconFlyCommonSection } from '../Common/SubMenuIconFlyCommonSection';
import { SubMenuCommonFly } from '../Common/SubMenuCommonFly';
// import { ACTIVECOMMANDMODE } from '../../../../AppDrawing/Core/CActiveCommandController';

// import { CPolyline } from '../../../../AppDrawing/Canvas/CGeometry/CPolyline';
// import { CRectangle } from '../../../../AppDrawing/Canvas/CGeometry/CRectangle';

export class MenuNImage extends MenuCommon {

    pullDownMenuWrapper: PullDownMenuWrapper;

    title: string;

    // tslint:disable-next-line:no-any
    constructor(pullDownMenuWrapper: PullDownMenuWrapper, ac: any, title: string, xOff: number) {
        super(pullDownMenuWrapper, ac);
        this.pullDownMenuWrapper = pullDownMenuWrapper;
        this.vec = new NVector3(this.pullDownMenuWrapper.vec.x + xOff, this.pullDownMenuWrapper.vec.y);
        this.height = this.pullDownMenuWrapper.height;
        this.title = title;
        this.width = (this.pullDownMenuWrapper.textOffX * 2) + (this.title.length * 5);
        this.Init();
    }
    Init() {
        this.AppendCommand();
        this.heightSub = this.pullDownMenuWrapper.theme.heightEach * this.menus.length;
        this.widthSub = this.pullDownMenuWrapper.theme.widthEach;
        this.vecSub = new NVector3(this.vec.x, this.vec.y + this.pullDownMenuWrapper.bar.height + 1);
    }
    AppendCommand() {
        let numInterval = 0, numSubInterval = 0;
        let m = new SubMenuIconCommon(this, 'dummy', this.pullDownMenuWrapper.bar.height);

        // .............................................................................................................................. sub flyout menu
        m = new SubMenuIconCommon(this, 'Abbout NImage', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        // m.isClickable = false;
        m.MouseDownPost = () => {
            this.pullDownMenuWrapper.nUI.independentPanelWrapper.activeCommand.AboutNImage();
        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        m = new SubMenuIconCommon(this, 'Open File', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        // m.isClickable = false;
        m.MouseDownPost = () => {
            this.ac.OpenImg();
            // this.pullDownMenuWrapper.viewController.njslabSandbox.viewController.indePanel.activeCommand.AboutVisCanvas();

        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        m = new SubMenuIconCommon(this, 'Save', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.MouseDownPost = () => {
            // this.ac.SetActiveCommand(ACTIVE_SKETCH_MODE.ERASER);
        };
        this.menus.push(m);

        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        // .............................................................................................................................. sub flyout menu
        m = new SubMenuCommonFly(this, 'Adjustment', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        let mFly = m as SubMenuCommonFly; numSubInterval = 0;

        let subM = new SubMenuIconFlyCommon(mFly, 'Option', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {
            // if (this.pullDownMenuWrapper.viewController.njslabSandbox.appDrawingBlock) {
            //     this.pullDownMenuWrapper.viewController.njslabSandbox.appDrawingBlock.activeCommandDrawingBlock.GetGeometryRPoint();
            // }
            //  this.pullDownMenuWrapper.viewController.njslabSandbox.ACController.Stoppropagation = true;this.pullDownMenuWrapper.Reset();
        };
        m.menus.push(subM);
        m.menus.push(new SubMenuIconFlyCommonSection(mFly, 'section', m.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++));

        subM = new SubMenuIconFlyCommon(mFly, 'Brightness/Contrast...', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {
            // this.ac.SetActiveCommand(ACTIVE_SKETCH_MODE.SIMPLE_PENCIL);
        };
        m.menus.push(subM);

        subM = new SubMenuIconFlyCommon(mFly, 'Hue/Saturation...', mFly.vec.y + this.pullDownMenuWrapper.theme.heightEach * numSubInterval++);
        subM.isClickable = false;
        subM.MouseDownPost = () => {
            // this.ac.SetActiveCommand(ACTIVE_SKETCH_MODE.MULTIPLE_LINE_PENCIL);
        };
        m.menus.push(subM);
        this.menus.push(m);

        // ..............................................................................................................................
        this.menus.push(new SubMenuCommonSection(this, 'section', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++));

        m = new SubMenuIconCommon(this, 'Setting', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {
            console.log('Setting!!');

        };
        this.menus.push(m);

        m = new SubMenuIconCommon(this, 'Blending', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {
            console.log('Reset!!');

        };
        this.menus.push(m);

        m = new SubMenuIconCommon(this, 'Capture screen', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {
            console.log('Reset!!');

        };
        this.menus.push(m);

        m = new SubMenuIconCommon(this, 'Reset', this.pullDownMenuWrapper.bar.height + this.pullDownMenuWrapper.theme.heightEach * numInterval++);
        m.isClickable = false;
        m.MouseDownPost = () => {

            // this.pullDownMenuWrapper.viewController.njslabSandbox.appVisCanvas.activeCommands.RemoveAllGeo();

        };
        this.menus.push(m);
    }

}