  public static KMeterFromGeoGPSPos(gV0: NVector3, gV1: NVector3): number {
    let R = 6378.137; // Radius of earth in KM // a = Equatorial radius (6,378.1370 km) // b = Polar radius (6,356.7523 km)
    let dLat = gV1.lat * Math.PI / 180 - gV0.lat * Math.PI / 180;
    let dLon = gV1.long * Math.PI / 180 - gV0.long * Math.PI / 180;
    let a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(gV0.lat * Math.PI / 180) * Math.cos(gV1.lat * Math.PI / 180) * Math.sin(dLon / 2) * Math.sin(dLon / 2);
    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    let d = R * c;
    return d; // * 1000; // meters
  }
  public static KMeterByTwoGeoPos(long0: number, lat0: number, long1: number, lat1: number) {
    let v0 = new NVector3(0, 0); v0.long = long0; v0.lat = lat0;
    let v1 = new NVector3(0, 0); v1.long = long1; v1.lat = lat1;
    return NVector3.KMeterFromGeoGPSPos(v0, v1);
  }
  public static KMeterFromGPSPos(gV0: NVector3, gV1: NVector3): number {
    let R = 6378.137; // Radius of earth in KM // a = Equatorial radius (6,378.1370 km) // b = Polar radius (6,356.7523 km)
    let dLat = gV1.y * Math.PI / 180 - gV0.y * Math.PI / 180;
    let dLon = gV1.x * Math.PI / 180 - gV0.x * Math.PI / 180;
    let a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(gV0.y * Math.PI / 180) *
      Math.cos(gV1.y * Math.PI / 180) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    let d = R * c;
    return d; // * 1000; // meters
  }
  public static Get1KM_Long_ByGeoUnit(long: number = 0, lat: number = 0): number {
    let v = new NVector3(long, lat, 0);
    let addition = 0.0;
    for (let i = 0; i < 10000; ++i) {
      let dis = NVector3.KMeterFromGPSPos(v, new NVector3(v.x + addition, v.y, v.z));
      if (1 < dis && dis < 1.001) {
        // console.log('result long', i, dis);
        return v.x + addition;
      }
      addition += 0.00001;
    }
    return -1;
  }
  public static Get1KM_lat_ByGeoUnit(long: number = 0, lat: number = 0): number {
    let v = new NVector3(long, lat, 0);
    let addition = 0.0;
    for (let i = 0; i < 10000; ++i) {
      let dis = NVector3.KMeterFromGPSPos(v, new NVector3(v.x, v.y + addition, v.z));
      if (1 < dis && dis < 1.001) {
        // console.log('result lat', i, dis);
        return v.y + addition;
      }
      addition += 0.00001;
    }
    return -1;
  }
  public static Get1KM_Lat_Long_GeoUnit(long: number = 0, lat: number = 0): NVector3 {
    let lng = NVector3.Get1KM_Long_ByGeoUnit(long, lat);
    let la = NVector3.Get1KM_lat_ByGeoUnit(long, lat);
    return new NVector3(lng, la, 0);
  }
