  public static CalculateFalloff(pt: NVector3, attractor: NVector3, strength: number, falloff: number): number {
    let v: NVector3 = NVector3.Sub(attractor, pt);
    let dist: number = v.Length();
    return dist * (strength * Math.exp(-falloff * dist * dist));
  }
  public static Attractor(pt: NVector3, attractor: NVector3, strength: number, falloff: number): NVector3 {
    let v: NVector3 = NVector3.Sub(attractor, pt);
    let dist = v.Length();
    v.Unitize();
    return v.Mult(strength * Math.exp(-falloff * dist * dist));
  }
  public static Explosion(pt: NVector3, Explosion: NVector3, strength: number, falloff: number): NVector3 {
    let v = NVector3.Sub(Explosion, pt);
    let dist = v.Length();
    v.Unitize();
    return v.Mult(-(strength * Math.exp(-falloff * dist * dist)));
  }
  public static Rotor(pt: NVector3, rotor: NVector3, strength: number, falloff: number): NVector3 {
    let v = NVector3.Sub(rotor, pt);
    v.Unitize();
    // v.Rotate(3.141592 / 2, NVector3.zAxis);
    // let dist: number = v.Length;
    // return (v.Mult (strength * (Math.exp(-falloff * dist * dist))));
    return NVector3.Origin();
  }

