  public static GetCosAngleByTwoVectors(v0: NVector3, v1: NVector3): number {
    return NVector3.DotProduct(NVector3.Normalize(v0), NVector3.Normalize(v1));
  }
  public static GetAngleByTwoVectorsRadian(v0: NVector3, v1: NVector3): number {
    return Math.acos(NVector3.GetCosAngleByTwoVectors(v0, v1));
  }
  public static GetAngleByTwoVectors(v0: NVector3, v1: NVector3): number {
    return NVector3.GetAngleByTwoVectorsRadian(v0, v1) * 180.0 / Math.PI;
  }
  public static GetRotationByWorldCoodinate(v: NVector3): number {
    let origin = new NVector3(1, 0, 0);
    return NVector3.GetAngleByTwoVectors(origin, v);
  }
  public static GetRotationByWorldCoodinateByTwo(v0: NVector3, v1: NVector3): number {
    let v = v1.Sub(v0);
    let origin = new NVector3(1, 0, 0);
    if (v.y > 0) {
      return NVector3.GetAngleByTwoVectors(origin, v) * -1;
    } else {
      return NVector3.GetAngleByTwoVectors(origin, v);
    }
  }
