  public static orientation(p: NVector3, q: NVector3, r: NVector3): number {
    // See https://www.geeksforgeeks.org/orientation-3-ordered-points/
    // for details of below formula.
    const val = (q.y - p.y) * (r.x - q.x) - (q.x - p.x) * (r.y - q.y);

    if (val === 0) { return 0; } // colinear

    return (val > 0) ? 1 : 2; // clock or counterclock wise
  }
  public static onSegment(p: NVector3, q: NVector3, r: NVector3): boolean {
    if (q.x <= Math.max(p.x, r.x) && q.x >= Math.min(p.x, r.x) && q.y <= Math.max(p.y, r.y) && q.y >= Math.min(p.y, r.y)) { return true; }
    return false;
  }
  public static isIntersect(p1: NVector3, q1: NVector3, p2: NVector3, q2: NVector3): boolean {
    // Find the four orientations needed for general and
    // special cases
    const o1 = this.orientation(p1, q1, p2);
    const o2 = this.orientation(p1, q1, q2);
    const o3 = this.orientation(p2, q2, p1);
    const o4 = this.orientation(p2, q2, q1);

    // General case
    if (o1 !== o2 && o3 !== o4) { return true; }

    // Special Cases
    // p1, q1 and p2 are colinear and p2 lies on segment p1q1
    if (o1 === 0 && this.onSegment(p1, p2, q1)) { return true; }

    // p1, q1 and q2 are colinear and q2 lies on segment p1q1
    if (o2 === 0 && this.onSegment(p1, q2, q1)) { return true; }

    // p2, q2 and p1 are colinear and p1 lies on segment p2q2
    if (o3 === 0 && this.onSegment(p2, p1, q2)) { return true; }

    // p2, q2 and q1 are colinear and q1 lies on segment p2q2
    if (o4 === 0 && this.onSegment(p2, q1, q2)) { return true; }

    return false; // Doesn't fall in any of the above cases
  }


  // end intersect
  public static IsInsideOfVecs(v: NVector3, vecs: NVector3[]): boolean {
    // ray-casting algorithm based on https://github.com/substack/point-in-polygon
    // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
    let x = v.x, y = v.y;
    let inside = false;
    for (let i = 0, j = vecs.length - 1; i < vecs.length; j = i++) {
      let xi = vecs[i].x, yi = vecs[i].y;
      let xj = vecs[j].x, yj = vecs[j].y;
      let intersect = yi > y !== yj > y && x < (xj - xi) * (y - yi) / (yj - yi) + xi;
      if (intersect) {
        inside = !inside;
      }
    }
    return inside;
  }
  public static IsInsideOfVecsByArray(v: number[], vecs: number[][]): boolean {
    // ray-casting algorithm based on https://github.com/substack/point-in-polygon
    // http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
    let x = v[0], y = v[1];
    let inside = false;
    for (let i = 0, j = vecs.length - 1; i < vecs.length; j = i++) {
      let xi = vecs[i][0], yi = vecs[i][1];
      let xj = vecs[j][0], yj = vecs[j][1];
      let intersect = yi > y !== yj > y && x < (xj - xi) * (y - yi) / (yj - yi) + xi;
      if (intersect) {
        inside = !inside;
      }
    }
    return inside;
  }
  public static IsInsideOfVecsByCrossing(v: NVector3, w: number, h: number, vs: NVector3[]) {
    if (this.IsInsideOfVecs(new NVector3(v.x - w * 0.5, v.y - h * 0.5), vs)) { return true; }
    if (this.IsInsideOfVecs(new NVector3(v.x + w * 0.5, v.y - h * 0.5), vs)) { return true; }
    if (this.IsInsideOfVecs(new NVector3(v.x + w * 0.5, v.y + h * 0.5), vs)) { return true; }
    if (this.IsInsideOfVecs(new NVector3(v.x - w * 0.5, v.y + h * 0.5), vs)) { return true; }
    return false;
  }
