  public static ConvexHull(vs: NVector3[]): NVector3[] {
    vs.sort(function (a: NVector3, b: NVector3) {
      return a.x === b.x ? a.y - b.y : a.x - b.x;
    });
    let lower: NVector3[] = [];
    for (let i = 0; i < vs.length; i++) {
      while (lower.length >= 2 && NVector3.Cross(lower[lower.length - 2], lower[lower.length - 1], vs[i]) <= 0) { lower.pop(); }
      lower.push(vs[i]);
    }
    let upper: NVector3[] = [];
    for (let i = vs.length - 1; i >= 0; i--) {
      while (upper.length >= 2 && NVector3.Cross(upper[upper.length - 2], upper[upper.length - 1], vs[i]) <= 0) { upper.pop(); }
      upper.push(vs[i]);
    }
    upper.pop(); lower.pop();
    for (let i = 0; i < upper.length; ++i) { lower.push(upper[i]); }
    return lower;
  }
  public static Cross(O: NVector3, A: NVector3, B: NVector3): number {
    return (A.x - O.x) * (B.y - O.y) - (A.y - O.y) * (B.x - O.x);
  }
  public static Cross_A_B_OUT(a: NVector3, b: NVector3, out: NVector3) {
    const ax = a.x, ay = a.y, az = a.z, bx = b.x, by = b.y, bz = b.z;
    out.x = ay * bz - az * by;
    out.y = az * bx - ax * bz;
    out.z = ax * by - ay * bx;
    return out;
  }
  public static CheckBoundIntersection(vs: NVector3[], p0: NVector3, p1: NVector3) {
    for (let i = 0; i < vs.length - 1; ++i) {
      if (this.CheckLineIntersection(vs[i], vs[i + 1], p0, p1)) {
        return true;
      }
    }
    return false;
  }
  // http://jsfiddle.net/justin_c_rounds/Gd2S2/light/
  public static CheckLineIntersection(p0: NVector3, p1: NVector3, p2: NVector3, p3: NVector3) {
    // if the lines intersect, the result contains the x and y of the intersection (treating the lines as infinite) and booleans for whether line segment 1 or line segment 2 contain the point
    let denominator: number = 0, a: number = 0, b: number = 0, numerator1: number = 0, numerator2: number = 0;
    let onLine0: boolean = false, onLine1: boolean = false;
    denominator = ((p3.y - p2.y) * (p1.x - p0.x)) - ((p3.x - p2.x) * (p1.y - p0.y));
    let result: NVector3 = new NVector3(0, 0, 0);
    if (denominator === 0) { return undefined; }
    a = p0.y - p2.y;
    b = p0.x - p2.x;
    numerator1 = ((p3.x - p2.x) * a) - ((p3.y - p2.y) * b);
    numerator2 = ((p1.x - p0.x) * a) - ((p1.y - p0.y) * b);
    a = numerator1 / denominator;
    b = numerator2 / denominator;

    // if we cast these lines infinitely in both directions, they intersect here:
    result.x = p0.x + (a * (p1.x - p0.x));
    result.y = p0.y + (a * (p1.y - p0.y));
    /*
            // it is worth noting that this should be the same as:
            x = p2.x + (b * (p3.x - p2.x));
            y = p2.x + (b * (p3.y - p2.y));
            */
    // if line1 is a segment and line2 is infinite, they intersect if:
    if (a > 0 && a < 1) {
      onLine0 = true;
    }
    // if line2 is a segment and line1 is infinite, they intersect if:
    if (b > 0 && b < 1) {
      onLine1 = true;
    }
    // if line1 and line2 are segments, they intersect if both of the above are true
    if (onLine0 && onLine1) {
      return result;
    } else { return undefined; }
  }
  // http://www.sunshine2k.de/coding/java/Polygon/Convex/polygon.htm
  public static CheckConvexity(vs: NVector3[]) {
    if (vs.length < 3) { return false; }

    let p: NVector3;
    let v: NVector3;
    let u: NVector3;
    let res = 0;

    for (let i = 0; i < vs.length; i++) {
      p = vs[i];
      let tmp = vs[(i + 1) % vs.length];
      v = new NVector3(tmp.x - p.x, tmp.y - p.y, 0);
      u = vs[(i + 2) % vs.length];

      if (i === 0) { // in first loop direction is unknown, so save it in res
        res = u.x * v.y - u.y * v.x + v.x * p.y - v.y * p.x;
      } else {
        let newres = u.x * v.y - u.y * v.x + v.x * p.y - v.y * p.x;
        if ((newres > 0 && res < 0) || (newres < 0 && res > 0)) { return false; }
      }
    }
    return true;
  }
