  public static RotateVecsLoal(vecs: NVector3[], angle: number): NVector3[] {
    let cptToLocal = NVector3.GetCenterVecFromVecs(vecs);
    let cptToWorld = new NVector3(-cptToLocal.x, -cptToLocal.y, -cptToLocal.z);
    let temp: NVector3[] = NVector3.MoveVecs(vecs, cptToWorld);
    temp = NVector3.RotateVecs(temp, angle);
    temp = NVector3.MoveVecs(temp, cptToLocal);
    return temp;
  }
  public static RotateVecs(vecs: NVector3[], angle: number) {
    let temp: NVector3[] = [];
    for (let i = 0; i < vecs.length; ++i) {
      temp.push(NVector3.RotateVec(vecs[i], angle));
    }
    return temp;
  }
  public static Reflect(v: NVector3, n: NVector3, strength: number = 1): NVector3 {
    // http://www.3dkingdoms.com/weekly/weekly.php?a=2
    // (2 * (v * n) * n - v ) * strength;

    v.x *= -1;
    v.y *= -1;
    v.z *= -1;

    const dot = this.DotProduct(v, n);
    return new NVector3(
      (2 * dot * n.x - v.x) *  strength,
      (2 * dot * n.y - v.y) *  strength,
      (2 * dot * n.z - v.z) *  strength,
    );
  }
  public static RotateVec(v: NVector3, angle: number): NVector3 {
    let cos = Math.cos(angle * (3.14159265358979 / 180.0));
    let sin = Math.sin(angle * (3.14159265358979 / 180.0));
    let x = v.x * cos - v.y * sin;
    let y = v.x * sin + v.y * cos;
    return new NVector3(x, y, v.z);
  }
  public static GetCenterVecFromVecs(vecs: NVector3[]): NVector3 {
    let nv = new NVector3(0, 0, 0);
    for (let i = 0, c = vecs.length; i < c; ++i) {
      nv.Add(vecs[i]);
    }
    nv.Mult(1.0 / vecs.length);
    return nv;
  }
