  public static MidVecGeo(p1: NVector3, p2: NVector3): NVector3 {
    // return new NVector3(p1.screenX + p2.screenX * 0.5, p1.screenY + p2.screenY * 0.5, 0);
    // return new NVector3(p1.x + p2.x * 0.5, p1.y + p2.y * 0.5, 0);
    return new NVector3(p1.x + ((p2.x + p1.x) * 0.5), p1.y + ((p2.y + p1.y) * 0.5), p1.z + ((p2.z + p1.z) * 0.5));
  }
  // intersection // https://gist.github.com/gordonwoodhull/50eb65d2f048789f9558
  // https://stackoverflow.com/questions/563198/whats-the-most-efficent-way-to-calculate-where-two-line-segments-intersect/1968345#1968345
  public static GetVecFromIntersection(vs: NVector3[]): NVector3[] | undefined {
    let newVes: NVector3[] = [];
    for (let j = 0; j < vs.length - 1; ++j) {
      for (let k = j + 1; k < vs.length - 1; ++k) {
        let v = NVector3.CheckLineIntersection(vs[j], vs[j + 1], vs[k], vs[k + 1]);
        if (v) { newVes.push(v); }
      }
    }
    if (newVes.length === 0) { return undefined; } else { return newVes; }
  }
  public static between(a: number, b: number, c: number) {
    return a - NVector3.eps <= b && b <= c + NVector3.eps;
  }
  public static segment_intersection(v0: NVector3, v1: NVector3, v2: NVector3, v3: NVector3) {
    var x = ((v0.x * v1.y - v0.y * v1.x) * (v2.x - v3.x) - (v0.x - v1.x) * (v2.x * v3.y - v2.y * v3.x)) /
      ((v0.x - v1.x) * (v2.y - v3.y) - (v0.y - v1.y) * (v2.x - v3.x));
    var y = ((v0.x * v1.y - v0.y * v1.x) * (v2.y - v3.y) - (v0.y - v1.y) * (v2.x * v3.y - v2.y * v3.x)) /
      ((v0.x - v1.x) * (v2.y - v3.y) - (v0.y - v1.y) * (v2.x - v3.x));
    if (isNaN(x) || isNaN(y)) {
      return false;
    } else {
      if (v0.x >= v1.x) {
        if (!NVector3.between(v1.x, x, v0.x)) { return false; }
      } else {
        if (!NVector3.between(v0.x, x, v1.x)) { return false; }
      }
      if (v0.y >= v1.y) {
        if (!NVector3.between(v1.y, y, v0.y)) { return false; }
      } else {
        if (!NVector3.between(v0.y, y, v1.y)) { return false; }
      }
      if (v2.x >= v3.x) {
        if (!NVector3.between(v3.x, x, v2.x)) { return false; }
      } else {
        if (!NVector3.between(v2.x, x, v3.x)) { return false; }
      }
      if (v2.y >= v3.y) {
        if (!NVector3.between(v3.y, y, v2.y)) { return false; }
      } else {
        if (!NVector3.between(v2.y, y, v3.y)) { return false; }
      }
    }
    return new NVector3(x, y);
  }
