import { NVector3 } from '../../../../../';
import { PullDownMenuWrapper } from '../../PullDownMenuWrapper';
import { SubMenuIconCommon } from './SubMenuIconCommon';
import { SubMenuCommonFly } from './SubMenuCommonFly';
import { MenuPreset } from '../../Core/MenuPreset';
export class SubMenuIconFlyCommonSection extends SubMenuIconCommon {
    pullDownMenuWrapper: PullDownMenuWrapper;

    title: string;
    menus: SubMenuIconCommon[] = [];

    heightSub: number;
    widthSub: number;

    vecSub: NVector3;
    offY: number;
    constructor(menu: SubMenuCommonFly, title: string, yOff: number) {
        super(menu.menu, title, yOff);
        this.vec = new NVector3(this.menu.vec.x + this.menu.pullDownMenuWrapper.theme.widthEach, yOff);
        this.height = this.menu.pullDownMenuWrapper.theme.heightEach;
        this.width = this.menu.pullDownMenuWrapper.theme.widthEach;
        this.offY = this.height * 0.5;
    }
    MouseDown() {
        for (let i = 0; i < this.menus.length; ++i) {
            if (this.menus[i].isHover) {
                this.menus[i].MouseDown();
            }
        }
    }
    get IsHover() {
        return this.isHover;
    }
    set IsHover(v: boolean) {
        this.isHover = v;
        for (let i = 0; i < this.menus.length; ++i) {
            this.menus[i].isHide = !v;
        }
    }
    RenderPost(ctx: CanvasRenderingContext2D): void {
        if (this.isHide) { return; }
        MenuPreset.SectionLine(ctx, this.vec, this.width, this.offY);
    }
}