import { NVector3 } from '../../../';
import { MouseEventData } from '../../../IO/EventInteraction/MouseInteraction';
import { NUI } from '../NUI';
import { MenuRenderer } from './Core/MenuRenderer';
import { UIWrapperBase } from '../Core/Base/UIWrapperBase';
import { ControllerTheme } from '../Core/ControllerTheme';
import { MenuBar } from './Menu/Common/MenuBar';
import { MenuCommon } from './Menu/Common/MenuCommon';

import { MenuNCanvas } from './Menu/Menu/MenuNCanvas';
import { MenuNCanvasVisualization } from './Menu/Menu/MenuNCanvasVisualization';
import { MenuNCreativeCoding } from './Menu/Menu/MenuNCreativeCoding';
import { MenuNDrawingBlock } from './Menu/Menu/MenuNDrawingBlock';
import { MenuNImage } from './Menu/Menu/MenuNImage';
import { MenuNAlgorithmEnv } from './Menu/Menu/MenuNAlgorithmEnv';


// import { MenuNTF } from './Menu/Menu/Env/MenuNTF';
import { MenuNTFCustom } from './Menu/Menu/Env/MenuNTFCustom';

import { MenuNJSLabCoreSandbox } from './Menu/Menu/MenuNJSLabCoreSandbox';
export class PullDownMenuWrapper extends UIWrapperBase {

    menuOff: number = 10;
    textOffX: number = 12;
    textOffY: number = 10;

    theme: ControllerTheme;
    bar: MenuBar;

    menus: MenuCommon[] = [];
    menuRenderer: MenuRenderer;
    mouse: NVector3;

    menuTemp: MenuCommon;

    constructor(nUI: NUI, x: number = 0, y: number = 0, width: number = 0, height: number = 0) {
        super();
        this.nUI = nUI;
        this.theme = this.nUI.theme;
        this.width = width;
        this.height = height;
        this.vec = new NVector3(x, y);

        this.textOffY = this.theme.textYOff;
        this.menuRenderer = new MenuRenderer(this);

        this.bar = new MenuBar(this, this.width, this.height);
        this.mouse = new NVector3(0, 0);
        this.BuildMenuPre();
    }
    BuildMenuPre() {
        this.menuTemp = new MenuNJSLabCoreSandbox(this, {}, 'NJSCoreSandbox', this.menuOff); this.menus.push(this.menuTemp);
        // this.menuTemp = new MenuLink(this, {}, 'Help', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);

    }
    // tslint:disable-next-line:no-any
    BuildMenuContent(app: string, ac: any) {
        // this.menus[this.menus.length - 1].Remove();
        // this.menus.pop();

        switch (app) {
            case 'NCanvas':
                this.menuTemp = new MenuNCanvas(this, ac, 'NCanvas', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                this.menuTemp = new MenuNCanvasVisualization(this, ac, 'Visualization', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                this.menuTemp = new MenuNCreativeCoding(this, ac, 'Creative Coding', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                // this.menuTemp = new MenuVisMapCanvas(this, ac, 'VisMap', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                break;
            // eslint-disable-next-line no-duplicate-case
            case 'NCanvas':
                this.menuTemp = new MenuNCanvas(this, ac, 'NCanvas', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                this.menuTemp = new MenuNCanvasVisualization(this, ac, 'Visualization', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                this.menuTemp = new MenuNCreativeCoding(this, ac, 'Creative Coding', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                // this.menuTemp = new MenuVisMapCanvas(this, ac, 'VisMap', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                break;
            case 'appThreeEnv':
                this.menuTemp = new MenuNImage(this, ac, 'NImage', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                break;
            case 'NTF':
                // this.menuTemp = new MenuNTF(this, ac, 'NTF', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                this.menuTemp = new MenuNTFCustom(this, ac, 'NTF', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                break;
            case 'NDrawingBlock':
                this.menuTemp = new MenuNDrawingBlock(this, ac, 'NDrawing Block', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                break;
            case 'AlgorithmEnv':
                this.menuTemp = new MenuNAlgorithmEnv(this, ac, 'AlgorithmEnv', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                // this.menuTemp = new MenuNCanvasVisualization(this, ac, 'Visualization', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                // this.menuTemp = new MenuNCreativeCoding(this, ac, 'Creative Coding', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                // this.menuTemp = new MenuVisMapCanvas(this, ac, 'VisMap', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
                break;
            default: break;
            // this.menuTemp = new MenuEnvironment(this, 'Environment', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
            // this.menuTemp = new MenuCanvasVisualization(this, 'Visualization', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
            // this.menuTemp = new MenuCreativeCoding(this, 'Creative Coding', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);
        }
        // this.menuTemp = new MenuLink(this, {}, 'Help', this.menus[this.menus.length - 1].vec.x + this.menus[this.menus.length - 1].width); this.menus.push(this.menuTemp);

    }
    AddMenu(app: string, subMenu: string, title: string, fn: () => void) {
        for (let i = 0; i < this.menus.length; ++i) {
            if (this.menus[i].title === app) {
                this.menus[i].AddMenu(subMenu, title, fn);
            }
        }
    }
    RemoveMenu(m: MenuCommon) {
        m.Remove();
        this.menus.splice(this.menus.indexOf(m), 1);
    }
    Resize(width: number, height: number) {
        this.width = width;
        this.bar.width = width;
    }
    RenderPost(ctx: CanvasRenderingContext2D) {
        /* */
    }
    MouseMove(e: MouseEventData) {
        // if (this.isHidden) { return; }
        // console.log('ddd');
        // console.log('move');
        this.mouse.x = e.x; this.mouse.y = e.y;
        this.menuRenderer.MouseMove(e);
    }
    MouseDown(e: MouseEventData) {
        this.menuRenderer.MouseDown(e);
    }
    Reset() {
        this.menuRenderer.Reset();
    }
    ShowMenu(v: boolean) {
        this.menuRenderer.ShowMenu(v);
    }
    Destroy() {
        this.menuRenderer.RemoveAll();
    }
    set stopPropagation(v: boolean) {
        this.nUI.stopPropagation = v; // .viewController.viewAppCoreWrapper.viewIOWrapper.eventInteraction.stopPropagation = v;
    }
}