export class Deltatime {
    public now: number;
    public delta: number;
    public frameRate: number;

    private last: number = 0;
    private timeLastRender: number;
    msLoop: number;
    constructor() {
        //
    }
    public UpdateFPS() {
        this.now = window.performance.now();
        this.msLoop = this.now - this.last;
        this.last = this.now;

        this.delta = this.delta + Math.min(1, (this.msLoop) / 1000);

        if (this.delta > this.frameRate) { this.delta = this.delta - this.frameRate; }

        this.delta = (Date.now() - this.timeLastRender) / 1000;
        this.timeLastRender = Date.now();
        this.frameRate = (1 / this.delta);

    }
    public get dt() {
        return this.delta;
    }
}