    pMap: DataPixelMap;
    data: NVector3[];
    isVisited: boolean;
    isGetData: boolean;
    temp: number;
    constructor(pMap: DataPixelMap, v: NVector3) {
        this.pMap = pMap;
        this.nPix = [];
        this.bVec = [];
        this.vec = v;
        this.data = [];
        this.isVisited = false;
        this.isGetData = true;
    }
    GetData() {
        if (this.isGetData) { return this.data.length; }
        return 0;
    }
