        //     let currentP = pixs[0];
        //     pixs.shift();
        //     for (let j = 0; j < currentP.nPix.length; ++j) {
        //         if (currentP.nPix[j].isVisited) { continue; }
        //         if (NVector3.IsInsideOfVecs(currentP.nPix[j].vec, vecs)) {

        //             // console.log(pixs.length);
        //             if (!currentP.nPix[j].isVisited) {
        //                 sum += currentP.nPix[j].data.length;
