import { NVector3 } from '../../../../../../';
import { MouseEventData } from '../../../../../../IO/EventInteraction/MouseInteraction';

import { IndeSubPanel } from './IndeSubPanel';
import { IndePanelBase } from './base/IndePanelBase';

import { IndePanelPreset } from '../../../Core/IndePanelPreset';

export class IndeSubMenu extends IndePanelBase {
    list: string[] = [];
    indeSubPanel: IndeSubPanel;
    width: number;
    height: number;
    vec: NVector3;
    strings: string[];
    stringSlice: string;
    index: number = 0;
    constructor(indeSubPanel: IndeSubPanel, i: number, v: string) {
        super(indeSubPanel.independentPanel.independentPanelWrapper);
        this.indeSubPanel = indeSubPanel;
        this.index = i;
        this.width = this.indeSubPanel.width - 10;
        this.height = this.indeSubPanel.textHeight;
        this.title = v;
        this.stringSlice = v;
        this.vec = new NVector3(this.indeSubPanel.vec.x + 5, this.indeSubPanel.vec.y + 5 + this.index * this.height);
        this.isMoveable = false;
        this.isHidden = true;
    }
    Translate(e: MouseEventData): void {
        if (!this.isMoveable) { return; }
    }
    TranslateByXY(xo: number, yo: number) {
        this.vec.x += xo; this.vec.y += yo;
    }
    MouseDown() {
        this.independentPanelWrapper.stopPropagation = true;
        // console.log(this.title);

        // console.log(this.indeSubPanel);
        if (this.indeSubPanel.independentPanel.divInput) {
            this.indeSubPanel.independentPanel.divInput.value = this.title;
        }
        this.BindingMouseDownPost(this.title);
    }
    BindingMouseDownPost(v: string) {
        this.indeSubPanel.independentPanel.ExecuteCommand(v);
    }
    RenderPost(ctx: CanvasRenderingContext2D) {
        this.vec.x = this.indeSubPanel.vec.x + 5;
        this.vec.y = this.indeSubPanel.vec.y + 5 + this.index * this.height;

        if (this.isHidden) { return; }
        if (this.IsHover) {
            let fillCol = this.theme.colBG;
            IndePanelPreset.RoundedRectFill(ctx, NVector3.GetBoundaryByVecWidthHeight(this.vec, this.width, this.height), 3, 0, fillCol);
        }

        if (this.IsHover) {
            ctx.fillStyle = this.theme.colSelectedBG;
        } else {
            ctx.fillStyle = '#ffffff';
        }

        ctx.textBaseline = 'middle';
        ctx.textAlign = 'left';
        ctx.font = 'lighter 11px Arial';
        ctx.shadowBlur = 0;
        // console.log(ctx.canvas.style.letterSpacing);
        ctx.shadowOffsetY = 0;
        ctx.shadowOffsetX = 0;
        this.strings = this.FragmentText(ctx, this.title.slice(0, 25), this.width - 5);
        ctx.fillText(this.strings[0], this.vec.x + 2, this.vec.y + (this.HeadHeight * 0.5) - 2);

    }
    // reference : http://sourcoder.blogspot.com/2012/12/text-wrapping-in-html-canvas.html
    private FragmentText(ctx: CanvasRenderingContext2D, text: string, maxWidth: number) {
        let words = text.split(' ');
        let lines: string[] = [];
        let line = '';
        if (ctx.measureText(text).width < maxWidth) { return [text]; }
        while (words.length > 0) {
            var split = false;
            while (ctx.measureText(words[0]).width >= maxWidth) {
                var tmp = words[0];
                words[0] = tmp.slice(0, -1);
                if (!split) {
                    split = true;
                    words.splice(1, 0, tmp.slice(-1));
                } else {
                    words[1] = tmp.slice(-1) + words[1];
                }
            }
            if (ctx.measureText(line + words[0]).width < maxWidth) {
                line += words.shift() + ' ';
            } else {
                lines.push(line);
                line = '';
            }
            if (words.length === 0) {
                lines.push(line);
            }
        }
        return lines;
    }
}