import { NVector3 } from '../../../../../';
import { MenuBase } from './Base/MenuBase';
// import {MenuPreset} from '../Core/MenuPreset';
import { PullDownMenuWrapper } from '../../PullDownMenuWrapper';
export class MenuBar extends MenuBase {
    // pullDownMenuWrapper: PullDownMenuWrapper;

    constructor(pullDownMenuWrapper: PullDownMenuWrapper, width: number, height: number) {
        super(pullDownMenuWrapper);
        // this.pullDownMenuWrapper = pullDownMenuWrapper;
        this.width = width;
        this.height = height;
        this.vec = new NVector3(this.pullDownMenuWrapper.vec.x, this.pullDownMenuWrapper.vec.y);

    }
    public RenderPost(ctx: CanvasRenderingContext2D) {
        // MenuPreset.RoundedRectFill
        ctx.fillStyle = this.pullDownMenuWrapper.theme.colBG;
        ctx.beginPath();
        ctx.rect(this.vec.x, 0, this.width, this.vec.y + this.height);
        // ctx.rect(this.vec.x, this.vec.y, this.width, this.height);
        ctx.closePath();
        ctx.fill();
    }
}