
export const Sepia = (r: number, g: number, b: number) => {
    var avg = 0.3  * r + 0.59 * g + 0.11 * b;
    return [avg + 100, avg + 50, avg, 255];
};
export const getFalseColor = (value: number): number[] => {
    const col: number[] = [];
    col.push(Math.round(getR(value) * 255));
    col.push(Math.round(getG(value) * 255));
    col.push(Math.round(getB(value) * 255));
    return col;
};
export const getR = (value: number, min = 0, max = 1) => {
    value = (value - min) / (max - min);
    if (value <= 0.25)
        return 0;
    else if (value <= 0.5)
        return (1020. * value - 255.) / 255.;
    else return 1;
};
export const getG = (value: number, min = 0, max = 1) => {
    value = (value - min) / (max - min);
    if (value <= 0.25)
        return (1020 * value) / 255;
    else if (value <= 0.5)
        return 1;
    else
        return (-510 * value + 510) / 255;
};
export const getB = (value: number, min = 0, max = 1) => {
    value = (value - min) / (max - min);
    if (value <= 0.25)
        return (-512 * value + 128) / 255;
    else return 0;
};
