    public static HexToRGBA(hex: string, visibility: number = 1, isGray: boolean = false) {
        hex = hex.replace('#', '');
        let r = parseInt(hex.substring(0, 2), 16);
        let g = parseInt(hex.substring(2, 4), 16);
        let b = parseInt(hex.substring(4, 6), 16);
        if (isGray) {
            // let v = (0.58 * (r / 255)) + (0.17 * (g / 255)) + (0.8 * (b / 255));
            let v = (0.58 * r) + (0.17 * g) + (0.8 * b);
            return [v, v, v, visibility];
        } else {
            return [r, g, b, visibility];
        }
    }
