import { NVector3 } from '../../../../../../..';
import { MouseEventData } from '../../../../../../../IO/EventInteraction/MouseInteraction';
import { IndependentPanelWrapper } from '../../../../IndependentPanelWrapper';
import { ControllerTheme } from '../../../../../Core/ControllerTheme';
export abstract class IndePanelBase {
    independentPanelWrapper: IndependentPanelWrapper;
    value: number;
    vec: NVector3;
    boundarVecs: NVector3[];
    isHidden: boolean;
    div: HTMLElement;
    height: number;
    width: number;
    minHeight: number = 100;
    minWidth: number = 200;
    HeadHeight: number = 25;
    divID: string;
    xOff: number;
    yOff: number;
    isMinimized: boolean = false;
    hasSubMenu: boolean = false;
    isSelected: boolean;
    isOver: boolean;
    isMoveable: boolean;
    style: number;
    filletR: number;
    title: string;
    colorBase: string;
    colorOver: string;
    colorText: string;
    isRender: boolean;
    transparent: number;
    theme: ControllerTheme;
    colorTop: string;
    abstract RenderPost(ctx: CanvasRenderingContext2D): void;

    constructor(independentPanelWrapper: IndependentPanelWrapper, width: number = 100, height: number = 20) {
        this.independentPanelWrapper = independentPanelWrapper;
        this.independentPanelWrapper.indePanelRender.Add(this);
        this.theme = independentPanelWrapper.theme;
        this.width = width;
        this.height = height;
        this.xOff = 10;
        this.yOff = 50;
        this.isHidden = false;
        this.isSelected = false;
        this.isOver = false;
        this.isMoveable = true;
        this.style = 0;
        this.filletR = 3;
        this.isRender = true;
        this.vec = new NVector3(100, 100, 0);
        this.title = 'Drawing Block';
        this.boundarVecs = [];
        // this.colorBase = '#907BD2';
        this.transparent = 1.0;
        // this.mapContainer = <HTMLElement>document.getElementById('mapContainer');

    }
    setTopColorTheme(v: string) {
        if (v === 'red') { this.colorTop = this.independentPanelWrapper.nUI.asset.themeRed.colSelectedBG; return; }
        if (v === 'blue') { this.colorTop = this.independentPanelWrapper.nUI.asset.themeBlue.colSelectedBG; return; }
        if (v === 'purple') { this.colorTop = this.independentPanelWrapper.nUI.asset.themePurple.colSelectedBG; return; }
        if (v === 'green') { this.colorTop = this.independentPanelWrapper.nUI.asset.themeGreen.colSelectedBG; return; }
        if (v === 'orange') { this.colorTop = this.independentPanelWrapper.nUI.asset.themeOrange.colSelectedBG; return; }
        if (v === 'gray') { this.colorTop = this.independentPanelWrapper.nUI.asset.themeGray.colSelectedBG; return; }
        if (v === 'darkGray') { this.colorTop = this.independentPanelWrapper.nUI.asset.themeDarkgray.colSelectedBG; return; }
        if (v === 'black') { this.colorTop = this.independentPanelWrapper.nUI.asset.themeBlack.colSelectedBG; return; }
    }
    Remove() {
        this.RemovePost();
        this.independentPanelWrapper.indePanelRender.Remove(this);
    }
    RemovePost() {
        //
    }
    DeepCopy() {
        /* */
    }
    Dummy() {
        /* */
    }
    get IsHidden() {
        return this.isHidden;
    }
    set IsHidden(v: boolean) {
        this.isHidden = v;
    }
    get IsHover() {
        return this.isOver;
    }
    set IsHover(v: boolean) {
        this.isOver = v;
    }
    get IsMinimized() {
        return this.isMinimized;
    }
    set IsMinimized(v: boolean) {
        this.isMinimized = v;
    }
    get HasSubMenu() {
        return this.hasSubMenu;
    }
    set HasSubMenu(v: boolean) {
        this.hasSubMenu = v;
    }
    get IsSelected() { return this.isSelected; }
    set IsSelected(v: boolean) { this.isSelected = v; }
    get Width() { return this.width; }
    set Width(v: number) { this.width = v; }
    get Height() { return this.height; }
    set Height(v: number) { this.height = v; }
    GetBoundaryVecs() {
        let theHeight = this.height * 0.5;
        let theWidth = this.width * 0.5;
        let v0 = new NVector3(this.vec.x - theWidth, this.vec.y - theHeight);
        let v1 = new NVector3(this.vec.x + theWidth, this.vec.y + theHeight);
        this.boundarVecs = NVector3.GetBoundingVecs(v0, v1);
        return this.boundarVecs;
    }
    ComputeBoundary() {
        this.boundarVecs = [];
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y + this.height, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y + this.height, this.vec.z));
        return this.boundarVecs;
    }
    ComputeBoundaryHead() {
        this.boundarVecs = [];
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y + this.HeadHeight, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y + this.HeadHeight, this.vec.z));
        return this.boundarVecs;
    }
    ComputeBoundaryPanel() {
        this.boundarVecs = [];
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y + this.HeadHeight, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width, this.vec.y + this.HeadHeight, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x + this.width - 15, this.vec.y + this.height - 15, this.vec.z));
        this.boundarVecs.push(new NVector3(this.vec.x, this.vec.y + this.height, this.vec.z));
        return this.boundarVecs;
    }
    IsMouseInside(x: number, y: number) {
        let v = NVector3.IsInsideOfVecs(new NVector3(x, y), this.ComputeBoundaryHead());
        this.isOver = v;
        return v;
    }
    IsMouseInsidePanel(x: number, y: number) {
        let v = NVector3.IsInsideOfVecs(new NVector3(x, y), this.ComputeBoundaryPanel());
        return v;
    }
    Translate(e: MouseEventData): void {
        if (!this.isMoveable) { return; }
        let xo = e.x - e.preX;
        let yo = e.y - e.preY;
        this.vec.x += xo;
        this.vec.y += yo;
        for (let i = 0; i < this.boundarVecs.length; ++i) { this.boundarVecs[i].x += xo; this.boundarVecs[i].y += yo; }
    }
    Move(x: number, y: number) {
        this.vec.x = x; this.vec.y = y;
    }
    HideToogle() {
        this.IsHidden = !this.IsHidden;
        this.Hide();
    }
    ExecuteCommand(v: string) {
        console.log(v + 'binding needed');
    }
    Hide() {
        /* */
    }
    MouseDown() {
        /* */
    }
    MouseUp(e: MouseEventData) {
        /* */
    }
    MouseDrag(e: MouseEventData) {
        this.Translate(e);
    }
    MouseDouble(e: MouseEventData) {
        /* */
    }
    MouseRight(e: MouseEventData) {
        /* */
    }
    Render(ctx: CanvasRenderingContext2D) {
        if (this.isHidden) { return; }
        this.RenderPost(ctx);
    }
}